const Constants = require("../src/constants");

class ColMapData {
    constructor(xml, cell, counter, html) {
        this.xml = xml || null;
        this.xmlEvents = cell.getDataSingleNode(xml, Constants.row.nodeName.events);
        cell.worksheet.workbook.adjustEvents(this.xmlEvents);
        if (!this.xmlEvents) {
            this.xmlEvents = xml.getAttribute(Constants.cell.attributes.onEvent) || null;
            // if (this.xmlEvents) {
            //     this.xmlEvents = HTMLParser.parse(this.xmlEvents).getElementsByTagName(Constants.row.nodeName.events)[0];
            //     dataDesignator.replaceDataDesignatorsXmlUpVersion(this.xmlEvents);
            // }
        }
        this.html = html || null;
        this.cell = cell || null;
        if (this.cell)
            this.cell.colData = this;
        this.tagNames = [];
        this.counter = counter;
    }
}
class ColMap {
    constructor() {
        this.colMap = null;
    }
    matchTemplate(tag) {
        const value = this.colMap.get(tag);
        return value ? value.clone() : null;
    }
    getTemplate(tagArray) {
        let html = null;
        tagArray.every(tag => {
            return !(html = this.matchTemplate(tag));
        });
        return html;
    }
}
module.exports = {ColMapData, ColMap};
