const HTMLParser = require("node-html-parser");
const pixelWidth = require("string-pixel-width");
// const dataDesignator = require("./data-designator");
const funcUtils = {
    convertToJavaScriptString: function(str, convertSingleQuote) {
        if (!str || typeof str !== "string")
            return str;
        let regexpsReplace = [
            {regexp: /\\/, replace: "\\\\"},
            {regexp: /"/g, replace: "\\\""},
            {regexp: /\n/g, replace: "\\n"},
            {regexp: /\r/g, replace: "\\r"}
        ];
        if (convertSingleQuote)
            regexpsReplace.push({regexp: /'/g, replace: "\\\'"});
        if (str.substring(0,1) === "\"" || str.substring(0,1) === "'")
            regexpsReplace.push({regexp: /\\n/g, replace: "<br>"});
        regexpsReplace.forEach(regexpReplace => {
            str = str.replace(regexpReplace.regexp, regexpReplace.replace);
        });
        return str;
    },
    convertToHtmlString: function(str) {
        if (!str)
            return str;
        const html = HTMLParser.parse(str);
        this.convertHtmlChildString(html);
        str = html.toString().replace(/"/g, "\\\"");
        return str;
    },
    convertHtmlChildString: function(html) {
        html.childNodes.forEach(child => {
            if (child.nodeType === HTMLParser.NodeType.TEXT_NODE)
                child._rawText = child._rawText.replace(/\\/g, "\\\\").replace(/</g, "&lt;").replace(/>/g, "&gt;").replace(/[\n\r]/g, "<br>");
            else {
                // mimic VBA code in output
                let styles = child.getAttribute("style");
                if (styles) {
                    const stylePairs = styles.split(";");
                    styles = "";
                    stylePairs.forEach(style => {
                        const pairs = style.split(":");
                        styles += pairs[0].trim() + ": " + pairs[1].trim() + ";";
                    });
                    child.setAttribute("style", styles);
                }
                const classes = child.getAttribute("class");
                if (classes)
                    child.setAttribute("class", classes);
                child.rawAttrs = child.rawAttrs.replace(/\\/g, "\\\\");
                if (child.childNodes.length)
                    this.convertHtmlChildString(child);
            }
        });
    },
    convertToBoolean: function(value) {
        return !Boolean(value) ? false : typeof value === "string" && value.toLowerCase().trim() === "true";
    },
    // decodeXml(xmlString) {
    //     if (!xmlString)
    //         return xmlString;
    //     const escaped_one_to_xml_special_map = {
    //         '&amp;': '&',
    //         '&quot;': '"',
    //         '&lt;': '<',
    //         '&gt;': '>'
    //     };
    //     return xmlString.replace(/(&quot;|&lt;|&gt;|&amp;)/g,
    //         function (string, item) {
    //             return escaped_one_to_xml_special_map[item];
    //         });
    // },
    ptToPx: function(pt) {
        return Math.round(pt * 1.333333333333333333);
        // return Math.round(pt * 1.33333);
    },
    pxToPt: function(px) {
        return px / 1.333333333333333333;
        // return px / 1.33333;
    },
    EmuToPt: function(emu) {
        return Number((emu / 914400 * 72).toFixed(2));
    },
    EmuToPx: function(emu) {
        return Math.floor(emu / 9525);
    },
    pxWidth: function(string, options) {
        try {
            return pixelWidth(string, options);
        } catch(e) {
            options.font = "Arial";
            return pixelWidth(string, options);
        }
    },
    objectToString: function(obj, name) {
        let objString = (name ? name + ":" : "") + (!Array.isArray(obj) ? "{" : "[");
        Object.entries(obj).forEach(([key, value]) => {
            if (value === undefined)
                return;
            if (objString.length > (name ? name.length + 2 : 1))
                objString += ",";

            if (!Array.isArray(obj))
                objString += key + ":";
            if (value === null)
                objString += "null";
            else {
                switch(typeof value) {
                    case "string":
                        objString += "\"" + value + "\"";
                        break;
                    case "object":
                        if (value.objectFormat)
                            objString += value.objectFormat;
                        else
                            objString += this.objectToString(value);
                        break;
                    default:
                        objString += value;
                        break;
                }
            }
        });
        objString += (!Array.isArray(obj) ? "}" : "]");
        return objString;
    },
    numberFormatMatchGroups: function(numFmt) {
        const regexp = /(".*")?(\[[<=>\w]*])?([-+\(]?)([$]\s?|\[\$[^-]{1,4}-[^-]{1,3}(-[^\]]{2,5})?]\s?)?((-?)[#0,]*[#0]{1,3}(\.0*)?)?(,*)?(\s%)?(_\)|\))?(\s?([$]\s?|\[\$[^-]{1,4}-[^-]{1,3}(-[^\]]{2,5})]))?([^;]*)?(;?)/g;
        // Groups: 1: prefix string 2: [Red] or [>1000000]; 3: Account or Negative; 4: currency before number 5: currency group
        // 6: number format 7: negative before number format, 8: digits
        // 9: commas 10: percentage, 11: accounting close or _),
        // 12: currency w/ space 13: currency 15: suffix string 16: semicolon
        const matches = [...numFmt.matchAll(regexp)];
        return matches.slice(0, matches.length - 1);
    },
    isCurrencyFormat: function(numFmt) {
        const regexp = /(_?\(?[$])|(\[\$.{1,3}-[\dA-Z]{3,4}])/;
        return typeof numFmt === "string" ? regexp.test(numFmt) : false;
    },
    createCurrencyFormat: function(numFmt, toNumberFormatGroups) {
        const nfGroups = this.numberFormatMatchGroups(numFmt);
        if (typeof toNumberFormatGroups === "string")
            toNumberFormatGroups = this.numberFormatMatchGroups(toNumberFormatGroups);
        let toNumberFormat = "";
        for (let [index, nfg] of nfGroups.entries()) {
            if (index >= toNumberFormatGroups.length)
                break;
            for (const i of [3,4,11,12])
                nfg[i] = toNumberFormatGroups[index][i];
            if (nfg[7] !== toNumberFormatGroups[index][7] && toNumberFormatGroups[index][7]) {
                nfg[6] = nfg[7].length ? nfg[6].slice(1) : nfg[6];
                nfg[6] = toNumberFormatGroups[index][7] + nfg[6];
            }
            for (const i of [5,7,8,13,14])
                nfg[i] = undefined;
            toNumberFormat = toNumberFormat.concat(nfg.slice(1, 17).join(""));
        }
        if (toNumberFormatGroups.length > nfGroups.length) {
            toNumberFormat += (toNumberFormat.substring(toNumberFormat.length-1) !== ";" ? ";" : "");
            for (const [index, nfg] of toNumberFormatGroups.slice(nfGroups.length).entries()) {
                nfg[6] = nfGroups[index][6];
                for (const i of [5,7,8,13,14])
                    nfg[i] = undefined;
                toNumberFormat = toNumberFormat.concat(nfg.slice(1, 17).join(""));
            }
        }
        return toNumberFormat;
    },
    JSDateToExcelDate: function(jsDate) {
        const serial_number = (25569.0 + ((jsDate.getTime() - (jsDate.getTimezoneOffset() * 60 * 1000)) / (1000 * 60 * 60 * 24)));
        return jsDate.getFullYear() === 1900 ? serial_number - 1 : serial_number;
    },
}
module.exports = funcUtils;