const path = require("path");
const fileUtils = require("../utils/file-utils");
const fs = require("fs");
const Log = require("../utils/log");
const extension = "-Web";

class WebInfo {
    constructor(workbook) {
        this.fd = null;
        this.log = [];
        this.filesToCopy = [];
        this.manifest = [];
        this.functions = [];
        this.events = [];
        this.namespaces = [];
        this.product = {};
        this.project = {};
        this.workbook = workbook;
    }

    init() {
        this.project = {
            folder: {main: "", web: "", js: "", webApp: {main: "", web: "", js: "", jsForrester: "", docs: "", project: "", info: "", projectWeb: ""}},
            file: {html: "index.html", js: "custom.js", zip: "", log: ""},
        };
        this.project.folder.main = path.dirname(this.workbook.workbookData.fullName);
        this.project.folder.web = path.join(this.project.folder.main, "web");
        this.project.folder.js = path.join(this.project.folder.web, "js");
        this.project.folder.webApp.main = path.join(this.project.folder.main, path.basename(this.workbook.workbookData.name, path.extname(this.workbook.workbookData.name)) + extension);
        // if (!fileUtils.directoryExists(this.project.folder.webApp.main))
        //     fs.mkdirSync(this.project.folder.webApp.main);
        this.project.folder.webApp.js = path.join(this.project.folder.webApp.main, "js");
        // if (!fileUtils.directoryExists(this.project.folder.webApp.js))
        //     fs.mkdirSync(this.project.folder.webApp.js);
        this.project.folder.webApp.jsForrester = path.join(this.project.folder.webApp.js, "forrester");
        this.project.folder.webApp.docs = path.join(this.project.folder.webApp.main, "docs");
        // if (!fileUtils.directoryExists(this.project.folder.webApp.docs))
        //     fs.mkdirSync(this.project.folder.webApp.docs);
        this.project.folder.webApp.project = path.join(this.project.folder.webApp.docs, "project");
        // if (!fileUtils.directoryExists(this.project.folder.webApp.project))
        //     fs.mkdirSync(this.project.folder.webApp.project);

        this.project.folder.webApp.info = path.join(this.project.folder.webApp.project, "info");
        // if (!fileUtils.directoryExists(this.project.folder.webApp.info))
        //     fs.mkdirSync(this.project.folder.webApp.info);
        for (const directory of [this.project.folder.webApp.main, this.project.folder.webApp.js, this.project.folder.webApp.docs, this.project.folder.webApp.project, this.project.folder.webApp.info]) {
            if (!fileUtils.directoryExists(directory))
                fs.mkdirSync(directory);
        }

        this.project.file.log = path.join(this.project.folder.webApp.info, "log.txt");
        if (fileUtils.fileExists(this.project.file.log))
            fs.unlinkSync(this.project.file.log);
        this.project.folder.webApp.projectWeb = path.join(this.project.folder.webApp.project, "web");

        this.product = {folder: {main: "", web: "", js: ""}, file: {html: 'indexTemplate.html'}};
        this.product.folder.main = process.env.TEIWebProduct;
        if (!this.product.folder.main) {
            this.workbook.log(Log.typeE, "'TEIWebProduct' environmental variable not defined", false);
            return false;
        }
        this.product.folder.web = path.join(this.product.folder.main, "web");
        this.product.folder.js = path.join(this.product.folder.web, "js");

        this.folderOrderWeb = [this.project.folder.web, this.product.folder.web];
        for (const folder of this.folderOrderWeb) {
            const templateHtml = path.join(folder, this.product.file.html);
            if (fileUtils.fileExists(templateHtml)) {
                this.project.file.templateHtml = templateHtml;
                break;
            }
        }
        this.folderOrderJS = [this.project.folder.js, this.product.folder.js];

        return true;
    }

    writeLogFile () {
        let logger = fs.createWriteStream(this.project.file.log, {flags: 'w+'});
        this.log.forEach(log => {
            logger.write(`${log.text}\n`);
        });
        logger.end();
    }

    fileCopyLocal(file) {
        file = path.normalize(file).replace(this.project.folder.web, "");
        let projectFile = path.join(this.project.folder.web, file);
        if (fileUtils.fileExists(projectFile))
            this.filesToCopy.push({file: file, ignoreError: false, toDirectory: this.project.folder.webApp.projectWeb});
    }

    copyFiles() {
        for (const fn of this.filesToCopy) {
            let fileCopyStatusArray = fileUtils.copyFilesToFolder(fn.file, (!fn.hasOwnProperty("fromDirectory") ? this.folderOrderWeb : fn.fromDirectory),
                                                                    (!fn.hasOwnProperty("toDirectory") ? this.project.folder.webApp.main : fn.toDirectory));
            fileCopyStatusArray.forEach(fileCopyStatus => {
                if ((fileCopyStatus.success && fileCopyStatus.copied) || (!fileCopyStatus.success && !fn.ignoreError))
                this.workbook.log(fileCopyStatus.severity, fileCopyStatus.message);
                if (fileCopyStatus.success || (!fileCopyStatus.copied && fn.ignoreError)) {
                    let manifest = fileCopyStatus.toFile || fileCopyStatus.file;
                    manifest = path.relative(this.project.folder.webApp.main, manifest);
                    this.manifest.push(manifest);
                }
            });
        }
    }

    putFunction(func, pre = false) {
        if (!func)
            return;
        if (!this.functions.filter(f => f.name === func).length)
            this.functions.push({name: func, pre: pre, rendered: false});
    }

}
module.exports = WebInfo;