const CompositeExtXform = require('../composite-ext-xform');
const IntegerXform = require("exceljs/lib/xlsx/xform/simple/integer-xform");

class CellPositionXform extends CompositeExtXform {
    constructor(options) {
        super();

        this.tag = options.tag;
        this.map = {
            'xdr:col': new IntegerXform({tag: 'xdr:col', zero: true}),
            'xdr:colOff': new IntegerXform({tag: 'xdr:colOff', zero: true}),
            'xdr:row': new IntegerXform({tag: 'xdr:row', zero: true}),
            'xdr:rowOff': new IntegerXform({tag: 'xdr:rowOff', zero: true}),
        };
    }
    reconcile() {
        this.model = {
            nativeCol: this.model.col,
            nativeColOff: this.model.colOff,
            nativeRow: this.model.row,
            nativeRowOff: this.model.rowOff,
        };
    }
}

module.exports = CellPositionXform;
