const HTMLParser = require("node-html-parser");
const Cell = require("exceljs/lib/doc/cell");
const Enums = require("exceljs/lib/doc/enums");
const Log = require("../utils/log");
const Constants = require("./constants");
const dataDesignator = require("../utils/data-designator");

Object.defineProperties(Cell.prototype, {
    valueOrResult : {
        get() {
            if (this.type === Enums.ValueType.Formula)
                return this.result;
            else if (this.type === Enums.ValueType.RichText) {
                let text = '';
                for (const richText of this.value.richText)
                    text += richText.text;
                return text;
            } else
                return this.value;
        }
    },
    colData : {
        get() {
            return this.xlc ? this.xlc.colData : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            if (!this.xlc.colData)
                this.xlc.colData = [];
            this.xlc.colData.push(v);
        }
    },
    controls : {
        get(){
            return this.xlc ? this.xlc.controls : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            if (!this.xlc.controls)
                this.xlc.controls = [];
            this.xlc.controls.push(v);
        }
    },
    directPrecedents : {
        get() {
            return this.xlc && this.xlc.directPrecedents ? this.xlc.directPrecedents : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            if (!this.xlc.directPrecedents)
                this.xlc.directPrecedents = [];
            this.xlc.directPrecedents.push(v);
        }
    },
    namePrecedents : {
        get() {
            return this.xlc && this.xlc.namePrecedents ? this.xlc.namePrecedents : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            if (!this.xlc.namePrecedents)
                this.xlc.namePrecedents = [];
            this.xlc.namePrecedents.push(v);
        }
    },
    functionPrecedents : {
        get() {
            return this.xlc && this.xlc.functionPrecedents ? this.xlc.functionPrecedents : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            if (!this.xlc.functionPrecedents)
                this.xlc.functionPrecedents = [];
            if (!this.xlc.functionPrecedents.includes(v))
                this.xlc.functionPrecedents.push(v);
        }
    },
    getData: {
        get() {
            try {
                return this.type === Enums.ValueType.String ? this.getDataXML(this.value) : null;
            } catch (e) {
                this.workbook.log(Log.typeE, `getData: ${e.message}`);
                return null;
            }
        }
    },
    getDataXML: {
        value:
            function (str) {
                const xml = HTMLParser.parse(str);
                const data = xml.getElementsByTagName(Constants.cell.nodeName.data)[0];
                // if (data && !this.worksheet.workbook.isAddIn)
                if (data && !this.worksheet.workbook.version.xmlUpVersion && !this.xmlUpVersion) {
                    const dataOriginal = data.clone(true);
                    dataDesignator.replaceDataDesignatorsXmlUpVersion(data);
                    if (data.outerHTML !== dataOriginal.outerHTML)
                        this.xmlUpVersion = true;
                }
                return data || null;
            }
    },
    // getDataNode: {
    //     value:
    //         function (nodeName, returnElements) {
    //             const data = this.getData;
    //             if (data === null)
    //                 return null;
    //             const elements = data.getElementsByTagName(nodeName);
    //             return elements.length ? (returnElements ? elements : elements[0]) : null;
    //         }
    // },
    getDataSingleNode: {
        value:
            function (data, nodeName) {
                return data ? data.childNodes.filter(child => child.rawTagName === nodeName)[0] :  data;
            }
    },
    getDataNodes: {
        value:
            function (data, nodeName) {
                return data ? data.childNodes.filter(child => child.rawTagName === nodeName) :  data;
            }
    },
    xmlUpVersion: {
        get() {
            if (!this.xlc)
                return undefined;
            return this.xlc.xmlUpVersion;
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            return this.xlc.xmlUpVersion = v;
        }
    },
    addressFull: {
        get() {
            return `${this.worksheet.name}!${this.address}`;
        }
    },
    addressFull$: {
        get() {
            return `${this.worksheet.name}!${this.$col$row}`;
        }
    },
    addressFullCodeName: {
        get() {
            return `${this.worksheet.codeName}!${this.address}`;
        }
    },
    addressFullReference: {
        get() {
            return `'${this.worksheet.name}'!${this.address}`;
        }
    },
    addressFullReference$: {
        get() {
            return `'${this.worksheet.name}'!${this.$col$row}`;
        }
    },
    addressFullReferenceCodeName: {
        get() {
            return `'${this.worksheet.codeName}'!${this.address}`;
        }
    },
    addressFullRowColArray: {
        get() {
            return `${this.worksheet.codeName}.cells[${this.row}][${this.col}]`;
        }
    },
    addressFullRowColCells: {
        get() {
            return `${this.worksheet.codeName}.Cells(${this.row},${this.col})`;
        }
    },
    htmlId: {
        get() {
            return this.addressFullCodeName;
        }
    },
    htmlControlId: {
        get() {
            return `${this.htmlId}_Control`;
        }
    },
    htmlLabelId: {
        get() {
            return `${this.htmlId}_Label`;
        }
    },
    htmlInfoBoxId: {
        get() {
            return `${this.htmlId}_InfoBox`;
        }
    },
    htmlChartId: {
        get() {
            return `${this.htmlId}_Chart`;
        }
    },
    htmlImgId: {
        get() {
            return `${this.htmlId}_Img`;
        }
    },
    htmlName: {
        get() {
            return this.name || "";
            // return definedName && definedName.length ? definedName : (this.worksheet.name+"_"+this.address);
        }
    },
    htmlDbEntity: {
        get() {
            return this.htmlName.length ? this.htmlName : (`${this.worksheet.name}_${this.address}`);
            // return this.htmlName || (this.worksheet.name+"_"+this.address);
        }
    },
    htmlCheckboxGroup: {
        get() {
            return `${this.worksheet.codeName}_cg${(this._row.section ? this._row.section.index : 0)}`;
        }
    },
    htmlValue: {
        get() {
            let value = "";
            (this.controls || []).forEach(control => {
                if (control.objectType === "Radio") {
                    // find the existing group box
                    let groupBox = this.worksheet.radioGroupBoxes.filter(gb => gb.groupBox === control.groupBox)[0];
                    if (!groupBox) {
                        groupBox = {groupBox: control.groupBox, index: this.worksheet.radioGroupBoxes.length + 1, cells: []};
                        this.worksheet.radioGroupBoxes = groupBox;
                    }
                    // it's possible that two colData are associated with one cell, so only do once per cell
                    if (!groupBox.cells.includes(this))
                        groupBox.cells.push(this);
                    this.worksheet.xlc.radioGroup = `${this.worksheet.codeName}_rg${groupBox.index}`;
                    value = groupBox.cells.indexOf(this) + 1; // value is the radio button index
                }
            });
            return value;
        }
    },
    htmlRadioGroup: {
        get() {
            let radioGroup = "";
            (this.controls || []).forEach(control => {
                if (control.objectType === "Radio") {
                    // find the existing group box
                    let groupBox = this.worksheet.radioGroupBoxes.filter(gb => gb.groupBox === control.groupBox)[0];
                    if (!groupBox) {
                        groupBox = {groupBox: control.groupBox, index: this.worksheet.radioGroupBoxes.length + 1, cells: []};
                        this.worksheet.radioGroupBoxes = groupBox;
                    }
                    radioGroup = `${this.worksheet.codeName}_rg${groupBox.index}`;
                }
            });
            return radioGroup;
        }
    },
    htmlSubmenuId: {
        get() {
            return `${this.worksheet.codeName}Sub${this._row.section.index}`;
        }
    },
    htmlSubmenuSectionId: {
        get() {
            return `${this.worksheet.codeName}Sub${this._row.section.index}-section`;
        }
    },
    htmlRefSubmenuSectionNextId: {
        get() {
            return `${this.worksheet.codeName}Sub${(this._row.section.index + 1)}-section`;
        }
    },
    convertData: {
        value:
            function(property, xml) {
                return xml && xml.getAttribute(property) || (this[property] !== undefined ? this[property] : "");
            }
    },
    getInfo: {
        value:
            function(name) {
                const info = {};
                this.colData.forEach(colData => {
                    if (colData.xml.getAttribute("name") === name) {
                        const infoAttr = colData.xml.getAttribute("info");
                        if (infoAttr) {
                            const attributes = infoAttr.split(";");
                            attributes.forEach(attribute => {
                                if (attribute.length) {
                                    const pairs = attribute.split(":");
                                    if (Number(pairs[1]))
                                        pairs[1] = Number(pairs[1]);
                                    info[pairs[0]] = pairs[1];
                                }
                            });
                        }
                    }
                });
                return info;
            }
    },
    hasFormulaEqualToOneCell: {
        get() {
            if (!this.formula)
                return false;
            // if (this.formula === "BenefitDetails!$E$10")
            let re = /[A-Za-z_][A-Za-z0-9_.\s]{1,30}/;
            let matchArray = this.formula.match(re) || [];
            if (matchArray && matchArray.length && matchArray[0].length === this.formula.length) {
                // could be a named cell
                if (this.worksheet.workbook.definedNameExists(this.formula)) {
                    const nameCells = this.worksheet.workbook.getDefinedNameCells(this.formula);
                    if (nameCells.length > 1)
                        return false;
                }
            } else {
                re = /('?[^\/\\*?]{1,31}'?!)?(([$]?([A-Z][A-Z]?|[A-X][A-F][A-D]))([$]?([0-9])+))?/;
                matchArray = this.formula.match(re) || [];
                if (!matchArray)
                    return false;
            }
            return !this.formula.replace(matchArray[0], "").length;
        }
    },
    parseFormulas : {
        get() {
            return this.xlc ? this.xlc.parseFormulas : [];
        },
        set(v) {
            if (!this.xlc)
                this.xlc = {};
            this.xlc.parseFormulas = v;
        }
    },
    parseFormula: {
        value:
            function() {
                let funcObj = {funcString: "", objectFormat: undefined};
                try {
                    funcObj.funcString = this.worksheet.workbook.parseFormula(this.type === Enums.ValueType.Error ? this.value.error : this.formula.replace(/_xlfn\./g, ""), this, `${this.addressFull}: equation parse error`, false);
                    // funcObj.func = new Function("return "+funcString);
                    funcObj.objectFormat = `() => ${funcObj.funcString}`;
                } catch (e) {
                    this.workbook.log(Log.typeE, `${this.addressFull}: equation parse error\n${funcObj.funcString}\n${this.formula}`);
                }
                return funcObj;
            }
    },
});
module.exports = Cell;