const Worksheet = require("exceljs/lib/doc/worksheet");
const Row = require("./row");
const Column = require("exceljs/lib/doc/column");
const Image = require('exceljs/lib/doc/image');
const Table = require('exceljs/lib/doc/table');
const DataValidations = require("exceljs/lib/doc/data-validations");
const Range = require("exceljs/lib/doc/range");
const colCache = require("exceljs/lib/utils/col-cache");
const Constants = require("./constants");
const HTMLParser = require("node-html-parser");
const funcUtils = require("../utils/func-utils");
const Log = require("../utils/log");
const Enums = require("exceljs/lib/doc/enums");
const dataDesignator = require("../utils/data-designator");

Object.defineProperties(Worksheet.prototype, {
    model : {
        get() {
            const model = {
                id: this.id,
                name: this.name,
                dataValidations: this.dataValidations.model,
                properties: this.properties,
                state: this.state,
                pageSetup: this.pageSetup,
                headerFooter: this.headerFooter,
                rowBreaks: this.rowBreaks,
                views: this.views,
                autoFilter: this.autoFilter,
                media: this._media.map(medium => medium.model),
                sheetProtection: this.sheetProtection,
                tables: Object.values(this.tables).map(table => table.model),
                conditionalFormattings: this.conditionalFormattings,
                // *** ADD ***
                controls: this._controls,
                shapes: this.shapes,
                charts: this.charts,
                // *** ADD END ***
            };

            // =================================================
            // columns
            model.cols = Column.toModel(this.columns);

            // ==========================================================
            // Rows
            const rows = (model.rows = []);
            const dimensions = (model.dimensions = new Range());
            this._rows.forEach(row => {
                const rowModel = row && row.model;
                if (rowModel) {
                    dimensions.expand(rowModel.number, rowModel.min, rowModel.number, rowModel.max);
                    rows.push(rowModel);
                }
            });

            // ==========================================================
            // Merges
            model.merges = [];
            _.each(this._merges, merge => {
                model.merges.push(merge.range);
            });

            return model;
        },
        set(value) {
            this.name = value.name;
            this._columns = Column.fromModel(this, value.cols);
            this._parseRows(value);

            this._parseMergeCells(value);
            this.dataValidations = new DataValidations(value.dataValidations);
            this.properties = value.properties;
            this.pageSetup = value.pageSetup;
            this.headerFooter = value.headerFooter;
            this.views = value.views;
            this.autoFilter = value.autoFilter;
            this._media = value.media.map(medium => new Image(this, medium));
            this.sheetProtection = value.sheetProtection;
            this.tables = value.tables.reduce((tables, table) => {
                const t = new Table();
                t.model = table;
                tables[table.name] = t;
                return tables;
            }, {});
            this.conditionalFormattings = value.conditionalFormattings;
            // *** ADD ***
            this._controls = value.controls;
            this.shapes = value.shapes;
            this.charts = value.charts;
            // *** ADD END ***
        }
    },
    loadXml : {
        value:
            function() {
                // if (!this.xlc)
                this.xlc = {};
                const cell = this.getCell("A1");
                this.xmlData = cell.getData;
                this.xml = cell.getDataSingleNode(this.xmlData, Constants.worksheet.nodeName.sheetData);
                // dataDesignator.replaceDataDesignatorsXmlUpVersion(this.xml);
                if (this.xml) {
                    this.xmlEvents = cell.getDataSingleNode(this.xml, Constants.worksheet.nodeName.events);
                    this.workbook.adjustEvents(this.xmlEvents);
                    // see if this sheet holds the workbook data
                    const xmlModel = cell.getDataSingleNode(this.xmlData, Constants.workbook.nodeName.model);
                    if (xmlModel) {
                        this.workbook.xmlData = this.xmlData;
                        this.workbook.xmlModel = xmlModel;
                        this.workbook.xmlModelTool = cell.getDataSingleNode(this.xmlData, Constants.workbook.nodeName.tool);
                        this.workbook.xmlModelWeb = cell.getDataSingleNode(this.xmlData, Constants.workbook.nodeName.web);
                        this.workbook.xmlModelWebApp = cell.getDataSingleNode(this.xmlData, Constants.workbook.nodeName.webApp);
                        this.workbook.sheetXmlModel = this;
                    }

                    if (!this.workbook.isAddIn) {
                        // see what state to put it in ... or delete
                        let state = this.xml.getAttribute(Constants.worksheet.attributes.finalizeToolWeb);
                        const usable = funcUtils.convertToBoolean(this.xml.getAttribute(Constants.worksheet.attributes.usableSheet));
                        // state: visible hidden veryHidden
                        if (!usable && this.state !== "visible") {
                            state = Constants.worksheet.visibility.delete
                            if (this.state === "hidden")
                                this.workbook.log(Log.typeG, `${this.name}: Marked for deletion as currently Hidden and Not Usable`);
                            else if (this.state === "veryHidden")
                                this.workbook.log(Log.typeG, `${this.name}: Marked for deletion as currently Very Hidden and Not Usable`);
                        }
                        if (state === Constants.worksheet.visibility.delete) {
                            this.delete = true;
                            return;
                        } else if (state === Constants.worksheet.visibility.show)
                            this.state = "visible";

                        if (state !== Constants.worksheet.visibility.delete) {
                            this.getRow(1).loadXml(); // load so can determine if need to delete columns
                            // see if should delete period columns
                            const colZero = this.getRow(1).getColDataByName("0");
                            // const colZero = (this.getRow(1).colData || []).filter(data => data.xml.getAttribute(Constants.cell.attributes.name) === "0")[0];
                            if (colZero) {
                                const col = colCache.l2n(colZero.xml.getAttribute(Constants.cell.attributes.colBegin));
                                this.deleteColumns = {
                                    colStart: col + this.workbook.workbookData.periods + 1,
                                    colCount: this.workbook.workbookData.maxPeriods - this.workbook.workbookData.periods,
                                };
                                this.deleteColumns.enabled = this.deleteColumns.colCount > 0;

                                // now delete columns
                                if (this.deleteColumns.enabled) {
                                    this.spliceColumns(this.deleteColumns.colStart, this.deleteColumns.colCount);
                                    (this.charts || []).forEach(chart => {
                                        // adjust charts if needed due to splicing columns
                                        if (chart.range.left >= this.deleteColumns.colStart && chart.range.left - this.deleteColumns.colCount > 0)
                                            chart.range.left -= this.deleteColumns.colCount;
                                        if (chart.range.right >= this.deleteColumns.colStart && chart.range.right - this.deleteColumns.colCount > 0)
                                            chart.range.right -= this.deleteColumns.colCount;
                                    });
                                }
                            }
                            if (state === Constants.worksheet.visibility.hide)
                                this.state = "hidden";
                            else if (state === Constants.worksheet.visibility.veryHidden)
                                this.state = "veryHidden";
                        }
                    }
                } else if (!this.workbook.isAddIn)
                    this.state = "hidden";

                this.xlc.sections = []; // reset as any previous loadXml will create sections
                this.eachRow({includeEmpty: false}, row => {
                    row.loadXml();
                });

                // associate controls to cells
                (this._controls || []).forEach(control => {
                    this.getCell(control.range.tl).controls = control;
                });

                this.updateCurrencyList();
            }
    },
    xmlData : {
        get() {
            return this.xlc.xmlData;
        },
        set(v) {
            this.xlc.xmlData = v;
        }
    },

    xmlDataSave: {
        values:
            function(context) {
                return context.workbook.worksheets.getItem(this.name).getRange(`A1`).values = [[this.xmlDataString]];
            }
    },

    xmlDataString : {
        get() {
            if (!this.xmlData)
                return '';
            return this.workbook.convertXml(this.xmlData).outerHTML;
        }
    },

    xml : {
        get() {
            if (!this.xlc)
                this.xlc = {};
            return this.xlc.xml;
        },
        set(v) {
            this.xlc.xml = v;
        }
    },
    navIndex : {
        get() {
            return this.xlc.navIndex;
        },
        set(v) {
            this.xlc.navIndex = v;
        }
    },
    sections : {
        get() {
            return this.xlc.sections || [];
        },
        set(v) {
            if (!this.xlc.sections)
                this.xlc.sections = [];
            this.xlc.sections.push(v);
        }
    },
    xmlEvents : {
        get() {
            return this.xlc.xmlEvents;
        },
        set(v) {
            this.xlc.xmlEvents = v;
        }
    },
    controls : {
        get() {
            return this.xlc.controls || [];
        }
    },
    sliders : {
        get() {
            return this.xlc.sliders || [];
        },
        set(v) {
            if (!this.xlc.sliders)
                this.xlc.sliders = [];
            this.xlc.sliders.push(v);
        }
    },
    currencyList : {
        get() {
            return this.xlc.currencyList || [];
        },
        set(v) {
            if (!this.xlc.currencyList)
                this.xlc.currencyList = [];
            this.xlc.currencyList.push(v);
        }
    },

    radioGroupBoxes : {
        get() {
            return this.xlc.radioGroupBoxes || [];
        },
        set(v) {
            if (!this.xlc.radioGroupBoxes)
                this.xlc.radioGroupBoxes = [];
            this.xlc.radioGroupBoxes.push(v);
        }
    },
    deleteColumns : {
        get() {
            return this.xlc.deleteColumns || {};
        },
        set(v) {
            this.xlc.deleteColumns = v;
        }
    },
    _orderNo: {
        // when sheets get deleted, this.orderNo is not updated, so need to recreate
        get() {
            return this.workbook.worksheets.findIndex(sheet => sheet.id === this.id);
        }
    },
    codeName: {
        get() {
            return `s${this._orderNo + 1}`;
        }
    },
    convertData: {
        value:
            function(property) {
                return this.xml.getAttribute(property) || (this[property] !== undefined ? this[property] : "");
            }
    },
    rangeCount: {
        get() {
            return (this.rowCount * this.columnCount);
        }
    },
    maxRangeAddress: {
        get() {
            if (this.rowCount <= 0 || this.columnCount <= 0)
                return "";
            return `A1:${this.getCell(this.rowCount, this.columnCount).address}`;
        }
    },
    isVeryHidden: {
        get() {
            return this.state === "veryHidden" || (this.xml && this.xml.getAttribute(Constants.worksheet.attributes.finalizeToolWeb) === Constants.worksheet.visibility.veryHidden);
        }
    },
    validSheet: {
        get() {
            return this.name !== Constants.worksheet.sheetNames.web;
        }
    },
    hasMetadata: {
        get() {
            return this.xml !== null;
        }
    },
    rangeAddress: {
        value:
            function(range) {
                const rangeArray = [(range.sheetName ? this.workbook.getWorksheet(range.sheetName).codeName : this.codeName), range.top, range.left];
                if (range.rowCount > 1 || range.columnCount > 1) {
                    rangeArray.push(range.bottom - range.top + 1);
                    rangeArray.push(range.right - range.left + 1);
                }
                return `new Range(${rangeArray.join(",")})`;
            }
    },
    colOffset: {
        value:
            function(number, colOff) {
                let offset = 0;
                this.columns.every(column => {
                    if (column.number >= number)
                        return false;
                    else if (column.hidden)
                        return true;
                    offset += column.width;
                    return true;
                });
                // 635 = 914400 / 72 / 20 https://startbigthinksmall.wordpress.com/2010/01/04/points-inches-and-emus-measuring-units-in-office-open-xml/
                offset += (colOff ? (colOff / 635 / 20) : 0);
                return offset;
            }
    },
    htmlId: {
        get() {
            return `${this.codeName}Id`;
        }
    },
    htmlSectionId: {
        get() {
            return `${this.codeName}-section`;
        }
    },
    updateCurrencyList: {
        value:
            function() {
                // add to $Lists
                let $lists = this.workbook.getWorksheet(Constants.worksheet.sheetNames.lists);
                if (!$lists) {
                    this.workbook.addWorksheet(Constants.worksheet.sheetNames.lists);
                    $lists = this.workbook.getWorksheet(Constants.worksheet.sheetNames.lists);
                }
                if (this._orderNo === 0) {
                    this.workbook.removeDefinedName(Constants.workbook.definedNames.resetList);
                    if ($lists._columns)
                        $lists.spliceColumns(1, 10);
                }
                $lists.state = "veryHidden";

                let rowNumber = $lists.dimensions.model.bottom + 1;
                (this.currencyList || []).forEach(range => {
                    const rangeArray = [`[${range.sheetName}]`, range.top, range.left, range.bottom - range.top + 1, range.right - range.left + 1];
                    $lists.getCell(rowNumber++, 1).value = rangeArray.join(",");
                });
                const range = new Range($lists.dimensions.top, $lists.dimensions.left, $lists.dimensions.bottom, $lists.dimensions.right, $lists.name);
                if ( (this.currencyList || []).length ) {
                    const name = this.workbook.definedNames.getRanges(Constants.workbook.definedNames.currencyList);
                    if (name.ranges.length)
                        this.workbook.definedNames.addEx(range, Constants.workbook.definedNames.currencyList);
                    else
                        this.workbook.definedNames.add(range.addressFull, Constants.workbook.definedNames.currencyList);
                }
            }
    },

    convertToHtml: {
        value:
            function () {
                let element = HTMLParser.parse("");
                const sheetFragment = {
                    fragment: element,
                    nodeElement: []
                };
                let skipSection = false;
                this.eachRow({includeEmpty: false}, row => {
                    skipSection = row.convertToHtml(skipSection, sheetFragment);
                });
                return sheetFragment;
            }
    },

    doModal: {
        value:
            function(modal) {
                if (!this.xml)
                    return false;
                let results = funcUtils.convertToBoolean(this.xml.getAttribute(Constants.worksheet.attributes.modal));
                if (modal && results)
                    return true;
                else if (!modal && results)
                    return false;
                else if (!modal && !results)
                    return true;
                return false;
            }
    },

    // ***** ADD-IN *****
    selectedRange : {
        get() {
            return this.xlc.selectedRange;
        },
        set(v) {
            this.xlc.selectedRange = v;
        }
    },

    isNAVSheet : {
        get() {
            if (!this.xml || !this.validSheet || !funcUtils.convertToBoolean(this.xml.getAttribute(Constants.worksheet.attributes.usableSheet)))
                return false;
            const ftw = this.xml.getAttribute(Constants.worksheet.attributes.finalizeToolWeb);
            if (ftw !== Constants.worksheet.visibility.show && ftw !== Constants.worksheet.visibility.hide)
                return false;
            const navTabCreate = funcUtils.convertToBoolean(this.xml.getAttribute(Constants.worksheet.attributes.navTabCreate));
            return navTabCreate === undefined || navTabCreate;
        }
    },
    isCombinedSheet: {
        get() {
            if (!this.isNAVSheet)
                return false;
            let combinedSheets = [];
            this.workbook.eachSheet( worksheet => {
                if (worksheet.isNAVSheet) {
                    const combineSheets = worksheet.xml.getAttribute(Constants.worksheet.attributes.combineSheets);
                    if (combineSheets)
                        combinedSheets = combinedSheets.concat(combineSheets.split(","));
                }
            });
            const tag = this.xml.getAttribute(Constants.worksheet.attributes.tag);
            for (const sheetNameOrTag of combinedSheets) {
                if (sheetNameOrTag === this.name || sheetNameOrTag === tag)
                    return true;
            }
            return false;
        }
    },
    refresh : {
        get() {
            return this.xlc.refresh;
        },
        set(v) {
            this.xlc.refresh = v;
        }
    },
    reload : {
        get() {
            return this.xlc.reload;
        },
        set(v) {
            this.xlc.reload = v;
        }
    },
    addToRows : {
        value:
            function (rangeXL) {
                this.eachRow((row, number) => {
                    row.eachCell( (cell, col) => {
                        const formula = rangeXL.formulas[number-1][col-1];
                        if (typeof formula === "string" && formula.length > 0 && formula.substring(0, 1) === "=") {
                            cell.type = Enums.ValueType.Formula;
                            cell.value = {formula: formula.substring(1), result: cell.value};
                        } else if (typeof formula === "string" && formula.length === 0) {
                            cell.type = Enums.ValueType.Null;
                            cell.value = null;
                        }
                        // if (rangeXL.numberFormat[number-1][col-1] !== "General")
                        cell.numFmt = rangeXL.numberFormat[number-1][col-1];
                        cell.font = {bold: false, italic: false, strike: false, underline: false};
                        cell.alignment = {vertical: 'bottom', horizontal: 'left'};
                        cell.protection = {locked: true, hidden: false};
                        cell.dataValidation = {formulae: []};
                    });
                });
            }
    },
    addPropertiesToRows : {
        value:
            function (rows) {
                this.eachRow((row, number) => {
                    row.hidden = rows[number-1].rowHidden;
                    row.height = rows[number-1].height;
                });
            }
    },
    addPropertiesToColumns : {
        value:
            function (columns) {
                for (const column of columns) {
                    this._columns[column.columnIndex].hidden = column.columnHidden;
                    this._columns[column.columnIndex].width = column.width;
                }
            }
    },

    insertRowsNew : {
        value:
            function (start, values, style = 'n') {
                for (const row of this._rows.slice(start - 1)) {
                    if (row) {
                        row._number += values.length;
                        for (const cell of row._cells) {
                            if (cell)
                                cell._address = `${colCache.n2l(cell._column._number)}${row.number}`;
                        }
                    }
                }
                const rows = [];
                for (const v of values) {
                    const row = new Row(this, start + rows.length);
                    row.values = v;
                    rows.push(row);
                }
                this._rows.splice(start - 1, 0, ...rows);
                for (const row of rows)
                    row.loadXml(true);
                if (style !== 'n') {
                    // copy over the styles
                    for (let i = 0; i < values.length; i++) {
                        if (style[0] === 'o' && this.findRow(values.length + pos + i) !== undefined) {
                            this._copyStyle(values.length + pos + i, pos + i, style[1] === '+');
                        } else if (style[0] === 'i' && this.findRow(pos - 1) !== undefined) {
                            this._copyStyle(pos - 1, pos + i, style[1] === '+');
                        }
                    }
                }
                return this.getRows(start, values.length);
            }
    },

    duplicateRowNew : {
        value:
            function(worksheet, rowToDuplicate, insertRow, count) {
                if (rowToDuplicate < 1 || insertRow < 1 || count < 1)
                    return;
                this.insertRowsNew(insertRow, Array(count).fill(worksheet.getRow(rowToDuplicate).values.slice(1)));
            }
    },

    deleteRows : {
        value:
            function(start, count = 1) {
                const row = this.getRow(start);
                for (const section of this.sections.toReversed()) {
                    // see if entire section is deleted
                    if (section.start.number >= start && section.end.number <= (start + count - 1))
                        this.sections.splice(this.sections.indexOf(section), 1);
                }
                // TODO handle if delete spans a section, e.g. delete starts in middle of section and goes after section end
                // const section = row.section;
                // if (section) {
                //     let rowEnd = section.end.number - count;
                //     if (rowEnd < section.start.number)
                //         section.end = undefined;
                //     // else
                //     //     section.end = this.getRow(rowEnd);
                //     if (!section.end || section.start === section.end)
                //         this.sections.splice(this.sections.indexOf(section), 1);
                // }
                this.workbook.definedNames.spliceRows(this.name, start, count); // TODO fix deleteRows so properly updates definedNames
                this._rows.splice(start - 1, count);
                for (const row of this._rows.slice(start - 1)) {
                    if (row) {
                        row._number -= count;
                        for (const cell of row._cells) {
                            if (cell)
                                cell._address = `${colCache.n2l(cell._column._number)}${row.number}`;
                        }
                    }
                }
            }
    },

    getSheetTemplate: {
        value:
            function() {
                if (!this.workbook.xmlModel)
                    return null;
                let templateSheet;
                if (this.xml) {
                    templateSheet = this.workbook.getWorksheet(this.xml.getAttribute(Constants.worksheet.attributes.templateSheet) || "");
                    if (this.workbook.version.date > "20221222")
                        templateSheet = this.workbook.getSheetByTagView("templates", this.workbook.xmlModel.getAttribute(Constants.workbook.attributes.viewName), this.xml.getAttribute(Constants.worksheet.attributes.tag)) || templateSheet;
                } else
                    templateSheet = this.workbook.getWorksheetByTagName(Constants.sheetTags.templates);
                return templateSheet;
            }
    },

    getSectionTemplate: {
        value:
            function(tagName) {
                const templateSheet = this.getSheetTemplate();
                if (!templateSheet) {
                    this.workbook.log(Log.typeE, Log.messageDescription(Log.MessageNum.TemplateSheetNotFound, {worksheet: this, sheetTag: this.xml.getAttribute(Constants.worksheet.attributes.templateSheet)}));
                    return false;
                }
                let templateSection =  templateSheet.getSectionByTagName(tagName);
                if (!templateSection) {
                    // not found, look at any of the template sheets
                    const templateSheets = this.workbook.getWorksheetsByTagName(Constants.sheetTags.templates);
                    for (const templateSheet1 of templateSheets) {
                        templateSection = templateSheet1.getSectionByTagName(tagName);
                        if (templateSection)
                            break;
                    }
                }
                if (!templateSection) {
                    this.workbook.log(Log.typeE, Log.messageDescription(Log.MessageNum.TemplateSheetNotFound, {worksheet: this, tableTag: tagName, templateSheet: templateSheet}));
                    return false;
                }
                return templateSection;
            }
    },

    getSectionsByTagName: {
        value:
            function(tagName) {
                return this.sections.filter(section => section.tableTag === tagName);
            }
    },

    getSectionByTagName: {
        value:
            function(tagName) {
                return this.getSectionsByTagName(tagName)[0];
            }
    },

    equationSections: {
        get() {
            return this.getSectionsByTagName(Constants.row.tags.equationDesignator);
            // return this.sections.filter(section => section.tableTag === Constants.row.tags.equationDesignator);
        }
    },

    getSelectedRange : {
        value:
            async function (context, update = false) {
                if (!this.selectedRange)
                    this.selectedRange = {};
                if (update)
                    this.selectedRange.last = Object.assign({}, this.selectedRange.active);
                let firstCell = undefined;
                let selectedRanges = undefined;
                const sheetXL = context.workbook.worksheets.getItem(this.name).load();
                const activeSheetXL = context.workbook.worksheets.getActiveWorksheet().load("name");
                await context.sync();
                const sheetXLVisibility = sheetXL.visibility;
                if (this.name !== activeSheetXL.name) {
                    if (sheetXL.visibility !== Constants.worksheet.visibility.Visible)
                        sheetXL.visibility = Constants.worksheet.visibility.Visible;
                    sheetXL.activate();
                    await context.sync();
                }
                let activeChart = context.workbook.getActiveChartOrNullObject();
                await context.sync();
                if (activeChart.isNullObject) {
                    try {
                        selectedRanges = context.workbook.getSelectedRanges().load();
                        await context.sync();
                    } catch(e) {
                        selectedRanges = undefined;
                        activeChart = {isNullObject: false, isShape: true}; // it's a shape
                    }
                }
                if (selectedRanges) {
                    const address = selectedRanges.address.split("!");
                    firstCell = {sheetName: address[0].replace(/'/g, ""), sheetTag: this.xml ? this.xml.getAttribute(Constants.worksheet.attributes.tag) : undefined, rowNumber: Number(address[1].replace(/[A-Z]/g, "")), colLetter: address[1].replace(/\d+/g, "")};
                    if (isNaN(firstCell.rowNumber)) {
                        this.selectedRange.rowNumber = address[1].split(":")[0];
                        firstCell.colLetter = "A";
                    }
                    firstCell.address = `${firstCell.colLetter}${firstCell.rowNumber}`;
                    firstCell.addressFull = `${firstCell.sheetName}!${firstCell.colLetter}${firstCell.rowNumber}`;
                }
                this.selectedRange.active = {
                        selectedRanges: selectedRanges ? selectedRanges.toJSON() : undefined,
                        activeChart: activeChart,
                        firstCell: firstCell,
                };
                if (!this.selectedRange.last)
                    this.selectedRange.last = Object.assign({}, this.selectedRange.active);
                sheetXL.visibility = sheetXLVisibility;
                if (this.name === activeSheetXL.name)
                    this.workbook.selectedRange = this.selectedRange;
                else
                    activeSheetXL.activate();
            }
    },

    selectedRangeSelect: {
        value:
            async function(context) {
                if (this.selectedRange && this.selectedRange.active.firstCell) {
                    const sheetXL = context.workbook.worksheets.getItem(this.name);
                    if (this.state !== Constants.worksheet.visibility.visible)
                        sheetXL.visibility = Constants.worksheet.visibility.Visible;
                    sheetXL.getRange(this.selectedRange.active.firstCell.address).select();
                    if (this.state !== Constants.worksheet.visibility.visible)
                        sheetXL.visibility = Constants.worksheet.visibility.Hidden;
                }
            }
    },

    insertSectionPlaceholder: {
        value:
            async function(context, tableTag, sectionRow) {
                // insert the start and end rows before doing a replace
                const templateSection = this.getSectionTemplate(tableTag);
                if (!templateSection)
                    return false;
                const templateSheet = templateSection.worksheet;
                const sheetXL = context.workbook.worksheets.getItem(this.name);
                const templateSheetXL = context.workbook.worksheets.getItem(templateSheet.name);
                // insert 2 rows
                sheetXL.getRange(`${sectionRow}:${sectionRow + 1}`).insert(Excel.InsertShiftDirection.down);
                // copy and insert start and end rows from template sheet (only copy metadata from rows)
                sheetXL.getRange(`${sectionRow}:${sectionRow}`).copyFrom(templateSheetXL.getRange(`${templateSection.start.number}:${templateSection.start.number}`));
                this.insertRowsNew(sectionRow, [[templateSection.worksheet.getCell(templateSection.start.number, 1).value]]);
                sheetXL.getRange(`${sectionRow}:${sectionRow}`).rowHidden = true;
                this.getRow(sectionRow).hidden = true;
                sheetXL.getRange(`${sectionRow + 1}:${sectionRow + 1}`).copyFrom(templateSheetXL.getRange(`${templateSection.end.number}:${templateSection.end.number}`));
                this.insertRowsNew(sectionRow + 1, [[templateSection.worksheet.getCell(templateSection.end.number, 1).value]]);
                sheetXL.getRange(`${sectionRow + 1}:${sectionRow + 1}`).rowHidden = true;
                this.getRow(sectionRow + 1).hidden = true;
                // await context.sync();
                return true;
            }
    },

    insertEquation: {
        value:
            async function(context, sectionRow, variableRows = 4, doNotUpdateTablesCharts = false) {
                // this.workbook.webInfo.log = [];
                await this.workbook.updateShapeSectionInfo(context);
                if (!await this.insertSectionPlaceholder(context, Constants.row.tags.equationDesignator, sectionRow))
                    return false;
                const section = this.getRow(sectionRow).section;
                await section.deleteAllCharts(context);

                if (!await section.replaceSection(context, {hideRows: true}))
                    return false;

                const cb = this.xml.getAttribute(Constants.worksheet.attributes.cb);
                const sheetXL = context.workbook.worksheets.getItem(this.name);
                for (const row of section.getRows()) {
                    if (!row.xml)
                        continue;
                    let deleteRow = false;
                    let updateRow = false;
                    const tag = row.xml.getAttribute(Constants.row.attributes.tag);
                    switch(tag) {
                        case Constants.row.tags.variableDesignator:
                            const rowsToAdd = variableRows - 1; // add more rows
                            if (rowsToAdd <= 0)
                                break;
                            sheetXL.getRange(`${row.number + 1}:${row.number + rowsToAdd}`).insert(Excel.InsertShiftDirection.down);
                            sheetXL.getRange(`${row.number + 1}:${row.number + rowsToAdd}`).copyFrom(sheetXL.getRange(`${row.number}:${row.number}`));
                            this.duplicateRowNew(this, row.number, row.number + 1, rowsToAdd);
                            break;
                        case `${Constants.row.tags.riskAdjustedDesignator}${Constants.worksheet.general.benefit}`:
                        case `${Constants.row.tags.riskAdjustedPctDesignator}${Constants.worksheet.general.benefit}`:
                            if (cb === Constants.worksheet.general.cost)
                                deleteRow = true;
                            else
                                updateRow = true;
                            break;
                        case `${Constants.row.tags.riskAdjustedDesignator}${Constants.worksheet.general.cost}`:
                        case `${Constants.row.tags.riskAdjustedPctDesignator}${Constants.worksheet.general.cost}`:
                            if (cb === Constants.worksheet.general.benefit)
                                deleteRow = true;
                            else
                                updateRow = true;
                            break;
                    }
                    if (deleteRow) {
                        sheetXL.getRange(`${row.number}:${row.number}`).delete(Excel.DeleteShiftDirection.up);
                        this.deleteRows(row.number, 1);
                    } else if (updateRow) {
                        row.xml.setAttribute(Constants.row.attributes.tag, tag.replace(new RegExp(`${Constants.worksheet.general.benefit}$`), '').replace(new RegExp(`${Constants.worksheet.general.cost}$`), ''));
                        row.xmlDataSave(context);
                    }
                    const attr = row.xml.getAttribute(Constants.row.attributes.if);
                    if (attr) {
                        const result = dataDesignator.evaluateIf(attr, this.workbook, row.getCell(1));
                        if (!result) {
                            sheetXL.getRange(`${row.number}:${row.number}`).delete(Excel.DeleteShiftDirection.up);
                            this.deleteRows(row.number, 1);
                        }
                    }
                }

                await section.tableUpdate(context);
                await section.updateValues(context);
                await this.workbook.reorderReferencesUpdateFormulas(context);
                if (!doNotUpdateTablesCharts)
                    await this.updateTablesCharts(context);
                return true;
            }
    },
    getColumnByName: {
        value:
            function(name) {
                const row = this.getRow(1).getColDataByName(name);
                if (row)
                    return row.xml.getAttribute(Constants.cell.attributes.colBegin);
            }
    },
    changeCurrencies: {
        value:
            async function(context, toNumberFormat) {
                const toNumberFormatGroups = funcUtils.numberFormatMatchGroups(toNumberFormat);
                const sheetXL = context.workbook.worksheets.getItem(this.name);
                for (const range of this.currencyList) {
                    for (let col = range.left; col <= range.right; col++) {
                        for (let row = range.top; row <= range.bottom; row++) {
                            const address = colCache.encodeAddress(row, col);
                            const numberFormat = this.getCell(row, col).numFmt;
                            const numberFormatNew = funcUtils.createCurrencyFormat(numberFormat, toNumberFormatGroups);
                            if (numberFormat !== numberFormatNew) {
                                sheetXL.getRange(`${address}:${address}`).numberFormat = [[numberFormatNew]];
                                this.getCell(row, col).numFmt = numberFormatNew;
                                // await context.sync();
                            }
                        }
                    }
                }
            }
    },
    deleteShapes: {
        value:
            async function(context, rowStart = 0, rowEnd = 0) {
                const sheetXL = context.workbook.worksheets.getItem(this.name).load("shapes/items");
                if (rowStart <= 0)
                    rowStart = 1;
                if (rowEnd <= 0) {
                    const usedRangeXL = sheetXL.getUsedRange().load('rowIndex, rowCount');
                    await context.sync();
                    rowEnd = usedRangeXL.rowIndex + usedRangeXL.rowCount;
                }
                if (rowStart > rowEnd)
                    rowStart = rowEnd;
                const rowsXL = sheetXL.getRange(`${rowStart}:${rowEnd}`).load('top, height');
                await context.sync();
                const shapesXL = [];
                for (const shapeXL of sheetXL.shapes.items)
                    shapesXL.push(shapeXL.load('name, top'));
                await context.sync();
                for (const shapeXL of shapesXL) {
                    if (shapeXL.top >= rowsXL.top && shapeXL.top <= (rowsXL.top + rowsXL.height)) {
                        shapeXL.delete();
                        this.workbook.log(Log.typeI, `${this.name}, ${shapeXL.name}: shape deleted`);
                    }
                }
                await context.sync();
            }
    },

    updateTablesCharts: {
        value:
            async function(context, activeRange = undefined) {
                let msg = '';
                if (!this.xml)
                    return false;
                else if (this.sheetProtection)
                    msg = 'Sheet protected';
                else if (!funcUtils.convertToBoolean(this.xml.getAttribute(Constants.worksheet.attributes.usableSheet)))
                    msg = 'Sheet not usable';
                if (msg.length) {
                    this.workbook.log(Log.typeE, `${this.name}: Failed updating tables/charts: ${msg}`);
                    return false;
                }

                // get tables and replace / update
                const tableDependencies = this.workbook.createTableDependencies(this.xml.getAttribute(Constants.worksheet.attributes.tag));
                for (const section of tableDependencies) {
                    let replace = section.start.xmlTableData.getAttribute(Constants.row.attributes.tableData.tableReplace);
                    replace = replace ? funcUtils.convertToBoolean(replace) : true;
                    this.workbook.log(Log.typeI, `*** ${section.address}, section '${section.tableTag}': replace=${replace}`);
                    if (replace)
                        await section.tableReplace(context);
                    else
                        await section.tableUpdate(context);
                }
                // update section charts
                for (const section of tableDependencies)
                    await section.createCharts(context);

                if (activeRange && activeRange.active.selectedRanges)
                    context.workbook.worksheets.getItem(activeRange.active.firstCell.sheetName).getRange(`${activeRange.active.firstCell.colLetter}${activeRange.active.firstCell.rowNumber}`).select();

                return true;
            }
    },

    async deleteAllCharts(context, logMessages = true) {
        const tableDependencies = this.workbook.createTableDependencies(this.xml.getAttribute(Constants.worksheet.attributes.tag));
        // const charts = [];
        for (const section of tableDependencies)
            await section.deleteCharts(context, logMessages);
        // await context.sync();
    },
});
module.exports = Worksheet;