const CompositeExtXform = require('../composite-ext-xform');
const StringXform = require('exceljs/lib/xlsx/xform/simple/string-xform');
const funcUtils = require("../../../../utils/func-utils");

class StringPointXform extends CompositeExtXform {
    constructor() {
        super();

        this.tag = "c:pt";
        this.map = {
            "c:v" : new StringXform({tag: "c:v"}),
        };
    }

    reconcile() {
        this.model.html = funcUtils.convertToHtmlString(this.model.v) || undefined;
    }
}

module.exports = StringPointXform;
