const Enums = require("exceljs/lib/doc/enums");
const colCache = require("exceljs/lib/utils/col-cache");
const Log = require("./log");
const fileUtils = require("./file-utils");
const funcUtils = require("./func-utils");
const Constants = require("../src/constants");

const dataConstants = {lcase: "{lcase}", lowerCase: "|lowerCase", inherited: "inherited"};

const dataDesignator = {
    replaceDataDesignators: function(str, workbook, cell = null, caTemplate = undefined, caElement = undefined) {
        if (typeof str !== "string")
            return str;
        if ((workbook === null || workbook === undefined) && cell !== null)
            workbook = cell.workbook;
        else if ((cell === null || cell === undefined) && workbook !== null)
            cell = workbook.worksheets[0].getCell("A1");
        let reg = /(\${|\$\()([A-Za-z\-+\d]+)\.([A-Za-z0-9_.\-+!,]+|['`][^'`]+['`])(\|lowerCase)?(\)|})/g;
        let matchArray = [];
        do {
            matchArray = str.match(reg) || [];
            for (let match of matchArray) {
                let m = match.substring(2, match.length-1);
                let i = m.indexOf(".");
                let object = m.substring(0, i);
                let property = m.substring(i+1);
                let lowerCase = false;
                if (property.includes(dataConstants.lowerCase)) {
                    lowerCase = true;
                    property = property.replace(dataConstants.lowerCase, "");
                }
                let strReplace = "";
                switch(object) {
                    case "config":
                        strReplace = workbook.xmlModelWebApp.getAttribute(property) || "";
                        break;
                    case "name":
                        if (workbook)
                            strReplace = workbook.getDefinedNameValue(property) || "";
                        // if (typeof strReplace === "string")
                        //     strReplace = funcUtils.convertToJavaScriptString(strReplace, true);
                        break;
                    case "range":
                        break;
                    case "file":
                        property = property.substring(1, property.length - 1);
                        property = this.replaceDataDesignators(property, workbook, cell, caTemplate, caElement);
                        strReplace = fileUtils.fileExists(property) ? "true" : "false";
                        break;
                    case "sheet":
                        let worksheetLocal = cell.worksheet;
                        let i = property.indexOf("!");
                        if (i >= 0) {
                            let sheetName = property.substring(0, i);
                            property = property.substring(i + 1);
                            worksheetLocal = workbook.getWorksheet(sheetName);
                            if (!worksheetLocal) {
                                const msg = `${sheetName}: sheet not found, sheet attribute ${property} ignored.`;
                                if (cell)
                                    workbook.log(Log.typeE, msg, false);
                                else
                                    console.log(msg);
                                break;
                            }
                        }
                        if (worksheetLocal)
                            strReplace = worksheetLocal.convertData(property);
                        break;
                    case "table":
                        let s = property.split('.');
                        if (s.length === 1 && cell.worksheet) {
                            const row = cell.worksheet.getRow(cell.row).section.start;
                            if (row && row.xmlTableData) {
                                strReplace = row.xmlTableData.getAttribute(property) || "";
                            }
                        } else if (s[0] === "inherited" && caElement) {
                            strReplace = caElement.getAttribute(s[1]);
                            if (strReplace === match)
                                strReplace = '';
                        }
                        break;
                    case "row":
                        strReplace = cell._row.convertData(property);
                        break
                    case "col":
                        strReplace = caElement ? (caElement.getAttribute(property) || "") : "";
                        break;
                    // case "web":
                    //     if (webVariables.hasOwnProperty(property))
                    //         strReplace = webVariables[property] || "";
                    //     break;
                    case "cell":
                        if (!cell)
                            break;
                        let structure = property.split(".");
                        property = structure[0];
                        if (structure.length === 1)
                            strReplace = cell.convertData(property, caElement);
                        else if (structure.length === 2) {
                            if (cell[structure[0]])
                                strReplace = cell[structure[0]][structure[1]];
                        } else {
                            switch (property) {
                                case "style":
                                    if (!cell.hasOwnProperty("style") || structure.length <= 1)
                                        break;
                                    switch (structure[1]) {
                                        case "alignment":
                                            if (structure.length <= 2)
                                                break;
                                            if (structure[2].indexOf("horizontal") === 0) {
                                                let s = structure[2].split("-");
                                                let tagNameInputs = ["INPUT", "SELECT", "TEXTAREA"];
                                                if ((tagNameInputs.includes(caTemplate.tagName) || cell.type !== Enums.ValueType.Null)) {
                                                    let ha = null;
                                                    if (cell.style.hasOwnProperty("alignment") && cell.style.alignment.hasOwnProperty("horizontal"))
                                                        ha = cell.style.alignment.horizontal;
                                                    else if (cell.type === Enums.ValueType.Number || cell.type === Enums.ValueType.Boolean ||
                                                        (cell.type === Enums.ValueType.Formula && typeof cell.result === "boolean"))
                                                        ha = "right";
                                                    else
                                                        ha = "left";
                                                    if (ha !== s[1])
                                                        strReplace = "text-" + ha;
                                                }
                                            }
                                            break;
                                        case "font":
                                            if (structure.length <= 2 || !cell.style.hasOwnProperty(structure[1]) || cell.type === Enums.ValueType.Null || cell.type === Enums.ValueType.Formula)
                                                break;
                                            if (structure[2] === "bold") {
                                                let bold = cell.style.font.bold;
                                                if (bold)
                                                    strReplace = "font-weight:bold;";
                                            }
                                            break;
                                    }
                                    break;
                            }
                        }
                        break;
                    case "sectionTagCount":
                        strReplace = cell.worksheet.getRow(cell.row).sectionTagCount(property);
                        break;
                    case "rowTagCount":
                        strReplace = cell.worksheet.getRow(cell.row).rowTagCount(property);
                        break;
                    case "html":
                        strReplace = caElement.getAttribute(property) || "";
                        break;
                    case "sectionRowCol":
                        // either section,row,column or row,column
                        let sectionRowCol = property.split(',');
                        if (sectionRowCol.length < 2)
                            break;
                        const src = {};
                        if (sectionRowCol.length === 3) {
                            src.section = cell.worksheet.getSectionByTagName(sectionRowCol[0]);
                            sectionRowCol = sectionRowCol.slice(1);
                        } else
                            src.section = cell._row.section;
                        if (src.section) {
                            const row = src.section.getRowByTag(sectionRowCol[0]);
                            if (row) {
                                const cell1 = cell.worksheet.getCell(row.number, colCache.l2n(sectionRowCol[1]));
                                if (cell1)
                                    strReplace = `=${cell1.addressFullReference}`;
                            }
                        }
                        break;
                    case "formula":
                        // str = property.substring(1, property.length - 1);
                        // match = str;
                        strReplace = property.substring(1, property.length - 1);
                        break;
                    case "trd":
                        const props = property.split(".");
                        property = props[0];
                        if (property.match(/srcSectionRowTagNumber(\s*[+-]\s*\d\s*)*/)) {
                            if (cell._row.xmlTableRowData) {
                                const srcSheetName = cell._row.xmlTableRowData.getAttribute(Constants.row.attributes.tableData.srcSheetName);
                                const srcSectionId = cell._row.xmlTableRowData.getAttribute(Constants.row.attributes.tableData.srcSectionId);
                                if (srcSheetName && srcSectionId) {
                                    const tag = props.length === 2 ? props[1] : props[2];
                                    const srcSection = workbook.getWorksheet(srcSheetName).sections.filter(section => section.tableMatches.includes(srcSectionId))[0];
                                    const srcRow = srcSection ? srcSection.getRows().filter(row => row.xml && row.xml.getAttribute(Constants.row.attributes.tag) === tag)[0] : null;
                                    if (srcRow)
                                        strReplace = `${props[1] === "absolute" ? "$" : ""}${(srcRow.number + Number(property.replace("srcSectionRowTagNumber", "")))}`;
                                }
                            }
                        } else if (property.match(/sectionRowTagNumber(\s*[+-]\s*\d\s*)*/)) {
                            const tag = props.length === 2 ? props[1] : props[2];
                            const srcRow = cell._row.xml ? cell._row.section.getRows().filter(row => row.xml && row.xml.getAttribute(Constants.row.attributes.tag) === tag)[0] : null;
                            if (srcRow)
                                strReplace = `${props[1] === "absolute" ? "$" : ""}${(srcRow.number + Number(property.replace("sectionRowTagNumber", "")))}`;
                        } else if (property.match(/columnLetter(\s*[+-]\s*\d\s*)*/))
                            strReplace = `${props[1] === "absolute" ? "$" : ""}${colCache.n2l(cell.col + Number(property.replace("columnLetter", "")))}`;
                        else if (property === "srcTableAttr") {
                            const attribute = props.length === 2 ? props[1] : props[2];
                            const boolean = props.length > 2 && props[1] === "boolean";
                            const srcAttribute = caElement.getAttribute(attribute);
                            strReplace = boolean ? funcUtils.convertToBoolean(srcAttribute) : srcAttribute;
                        } else
                            strReplace = cell._row.xmlTableRowData ? cell._row.xmlTableRowData.getAttribute(property) : "";
                        break;
                    case "tableCalc":
                        const row = cell.worksheet.getRow(cell.row);
                        switch(property.toLowerCase()) {
                            case "sum":
                                if (cell.row > row.section.start.number) {
                                    const rowPrev = cell.worksheet.getRow(cell.row - 1);
                                    if (rowPrev.xml) {
                                        let address = "";
                                        const tag = rowPrev.xml.getAttribute(Constants.row.attributes.tag);
                                        if (tag) {
                                            const rows = cell.worksheet._rows.slice(row.section.start.number, cell.row-1).reverse();
                                            for (const r of rows) {
                                                const tag1 = r.xml ? r.xml.getAttribute(Constants.row.attributes.tag) : undefined;
                                                if (tag1 === tag)
                                                    address = `${cell.worksheet.getRow(cell.row - 1).getCell(cell.col).$col$row}:${cell.worksheet.getRow(r.number).getCell(cell.col).$col$row}`;
                                            }
                                            if (address.length)
                                                strReplace = `=SUM(${address})`;
                                        }
                                    }
                                }
                                break;
                            case "pcttotal":
                                if (row.xml) {
                                    const rowTag = row.xml.getAttribute(Constants.row.attributes.tag);
                                    if (rowTag) {
                                        const cells = row._cells.splice(1, cell.col - 2).reverse();
                                        let col = cell.col - 1;
                                        for (const c of cells) {
                                            if (c.type !== Enums.ValueType.Null) {
                                                col = c.col;
                                                break;
                                            }
                                        }
                                        const totalRow = row.section.getRows().filter(r => r.number > cell.row && r.xml && r.xml.getAttribute(Constants.row.attributes.tag) !== rowTag)[0];
                                        if (totalRow)
                                            strReplace = `=IF(${cell.worksheet.getRow(totalRow.number).getCell(col).$col$row}<>0,${cell.worksheet.getRow(cell.row).getCell(col).$col$row}/${cell.worksheet.getRow(totalRow.number).getCell(col).$col$row},"----")`;
                                    }
                                }
                                break;
                        }
                        break;
                }
                if (lowerCase || strReplace === "True" || strReplace === "False")
                    strReplace = strReplace.toLowerCase();
                str = str.replace(match, strReplace);
            }
        } while (matchArray.length);

        str = this.replaceDataDesignatorsIf(str, workbook);

        return str;
    },
    replaceDataDesignatorsIf: function(str, workbook) {
        // see if any if statements
        // if((ifstring),(valuestring))
        let reg = /(\${)?(if\(\()([\w\s'"(){}=<>|&!.\-$])+(\),\()([\w\s'"()=.;:\-#{}]+(\)\)))}?/g;
        let matchArray = str.match(reg) || [];
        for (const match of matchArray) {
            let m = match.replace(/^\${/, '').replace(/}$/, '');
            m = m.substring(3, match.length - 4); // get rid of outer if(...)
            let arr = m.split(",");
            arr[0] = arr[0].substring(1, arr[0].length - 1); // get rid of outer ()
            arr[1] = arr[1].substring(1, arr[1].length - 1);  // get rid of outer ()
            str = this.evaluateIf(arr[0], workbook, null) ? str.replace(match, arr[1]) : str.replace(match, "").replace("  ", " ");
        }
        return str.trim();
    },
    replaceDataDesignatorsXmlUpVersion: function(xml) {
        if (!xml)
            return;
        Object.entries(xml.attributes).forEach(([attribute, value]) => {
            if (attribute === "onEvent")
                return; // handled separately
            value = this.replaceDataDesignatorsIfUpVersion(value, attribute);
            value = this.replaceDataDesignatorsBootstrapUpVersion(value);
            value = this.replaceDataDesignatorsUpVersion(value);
            xml.setAttribute(attribute, value);
        });
        if (xml.childNodes) {
            xml.childNodes.forEach(child => {
                this.replaceDataDesignatorsXmlUpVersion(child);
            });
        }
    },
    replaceDataDesignatorsUpVersion: function(str) {
        //{{tableData}}, {^sheetData^}, (~rowData~}, {!namedCell!}, {+range+}, {**rowTagTotal**}, {*rowTagCount*}, {%tableData (inherited)%}, {(sectionTagCount)}, {-sectionTagCount, not including excluded-}, {[sectionTagTotal]), {#colCount#}, {:webapp data:}
        // let regexArray = [/{:([A-Za-z0-9_]*):}/g,/{!([A-Za-z0-9_]*)!}/g,/{\^([A-Za-z0-9_!]*)\^}/g,/{{([A-Za-z0-9_]*)}}/g,/{\*([A-Za-z0-9_]*)\*}/g,/{\(\)}/g,/{\(\(\)\)}/g];
        // let object = ["model", "name", "sheet", "table", "rowTagCount", "sectionTagCount.any", "sectionTagCount.included"];
        if (typeof str !== "string")
            return str;
        // if (str.includes('{%'))
        //     console.log('replaceDataDesignatorsUpVersion');
        const upExpressions = [
            {regexp: /{:([A-Za-z0-9_]*):}/g, object: "model", property: "", modifier: ""},
            {regexp: /{!([A-Za-z0-9_]*)!}({lcase})?/g, object: "name", property: "", modifier: ""},
            {regexp: /{\^([A-Za-z0-9_!]*)\^}/g, object: "sheet", property: "", modifier: ""},
            {regexp: /{{([A-Za-z0-9_]*)}}/g, object: "table", property: "", modifier: ""},
            {regexp: /{%([A-Za-z0-9_]*)%}/g, object: "table", property: "", modifier: ".inherited"},
            {regexp: /{~([A-Za-z0-9_]*)~}/g, object: "row", property: "", modifier: ""},
            {regexp: /{\*([A-Za-z0-9_]*)\*}/g, object: "rowTagCount", property: "", modifier: ""},
            {regexp: /{\((.*)\)}/g, object: "sectionTagCount", property: "this", modifier: ""},
            {regexp: /{\(\((.*)\)\)}/g, object: "sectionTagCount", property: "included", modifier: ""},
            {regexp: /{-(.*)-}/g, object: "sectionTagCount", property: "included", modifier: ""},
            {regexp: /{&(.*)&}/g, object: "sectionRowCol", property: "", modifier: ""},
            {regexp: /{\+([A-Za-z0-9_]*)\+}/g, object: "range", property: "", modifier: ""},
            {regexp: /\^\^\[(.*)]\^\^/g, object: "tableCalc", property: "", modifier: ""},
            {regexp: /\^\^(.*)\^\^/g, object: "formula", property: "", modifier: "", inQuotes: true},
            {regexp: /~{([^}~]*)}~/g, object: "trd", property: "", modifier: ""},
        ];
        upExpressions.forEach( (expression) => {
            const matchArray = str.match(expression.regexp) || [];
            matchArray.forEach(match => {
                const object = expression.object;
                let property = expression.property;
                const regexp = new RegExp(expression.regexp);
                const matchGroup = regexp.exec(match);
                if (matchGroup.length > 1 && matchGroup[1].length)
                    property = matchGroup[1];
                let lowerCase = "";
                if (match.includes(dataConstants.lcase)) {
                    lowerCase = dataConstants.lowerCase;
                    const matchNew = match.replace(dataConstants.lcase, "");
                    property = matchNew.substring(2, matchNew.length-2) || expression.property;
                }
                const quote = expression.inQuotes ? "`" : "";
                const strReplace = `$\{${object}${expression.modifier}.${quote}${property}${lowerCase}${quote}\}`;
                str = str.replace(match, strReplace);
            });
        });
        return str;
    },
    replaceDataDesignatorsBootstrapUpVersion: function(str) {
        const re = /(b\d[+]?,?)+:(([\w\-{}]+)|(\[(([\w\-"'=\s{}] ?)+)]))/g;
        const matchArray = str.match(re) || [];
        matchArray.forEach(match => {
            const pairs = match.split(":");
            const bpairs = pairs[0].split(",");
            let bpairsReplace = "";
            bpairs.forEach( b => {
                const b1 = b.replace("b", "");
                const version = parseInt(b1);
                let strReplace = `$(config.bootstrap)${(b.indexOf("+") >= 0 ? ">=" : "===")}${version}`;
                if (bpairsReplace)
                    strReplace = "||" + strReplace;
                bpairsReplace += strReplace;
            });
            const property = pairs[1].replace("[", "").replace("]", "");
            const strReplace = `\${if((${bpairsReplace}),(${property}))}`;
            str = str.replace(match, strReplace);
        });
        return str;
    },
    replaceDataDesignatorsIfUpVersion: function(str, attribute) {
        const re = /if:\[(.)+]\[.*]/g;
        const matchArray = str.match(re) || [];
        matchArray.forEach(match => {
            const pairs = match.split("][");
            pairs[0] = pairs[0].replace("if:[", "");
            pairs[0] = this.replaceDataDesignatorsIfIfUpVersion(pairs[0]);
            pairs[1] = pairs[1].replace("]", "");
            const strReplace = `$\{if((${pairs[0]}),(${pairs[1]}))}`;
            str = str.replace(match, strReplace);
        });
        if (attribute === "if")
            str = this.replaceDataDesignatorsIfIfUpVersion(str);
        return str;
    },
    replaceDataDesignatorsIfIfUpVersion: function(str) {
        str = str.replace(/^True/, "true").replace(/^False/, "false"); // match at start of string
        str = str.replace(/True$/, "true").replace(/False$/, "false"); // match at end of string
        let matchArray = str.match(/([^=]+)(=)(true|false|\d+|'[^']*')$/); // match something=expression at end of string
        if (matchArray)
            str = `${matchArray[1]}===${matchArray[3]}`;
        matchArray = str.match(/(.+)(<>)(true|false|\d+|'[^']*')$/); // match something<>expression at end of string
        if (matchArray)
            str = `${matchArray[1]}!==${matchArray[3]}`;
        return str;
    },
    evaluateIf: function(f, workbook, cell, element = undefined) {
        if (!f.trim().length)
            return true;
        if (workbook == null && cell !== null)
            workbook = cell.workbook;
        const f1 = this.replaceDataDesignators(f, workbook, cell, [], element);
        try {
            // since eval() is a security threat, only accept limited expressions. Match, e.g.:
            // true===false !false  4===5  4>=5  3>2 'Hello'==='Goodbye' !('white'==='white')  !!'Hello'
            const exp = /^(\s*(!{0,2}\s*\(?\s*)(true|false|\d+|'[^']*')(\s*(===|>=|<=|>|!==)\s*(!{0,2}\s*)(true|false|\d+|'[^']*'))?(\s*(&&|\|\|))?\s*\)?)+/;
            let match = f1.match(exp);
            if (!match || match[0].length !== f1.length) {
                const msg = `Error evaluating expression: ${f}:::${f1}`;
                workbook.log(Log.typeE, msg, false);
                return false;
            }
            const v = eval(f1);
            return v === undefined ? false : v;
        } catch(e) {
            const msg = `Error evaluating expression: ${f}:::${f1}`;
            workbook.log(Log.typeE, msg, false);
            return false;
        }
    },
    getAttributes: function(xml, attributes, requiredList, workbook, cell) {
        const data = {};
        for (const [key, value] of Object.entries(attributes)) {
            if (typeof value !== "string")
                continue;
            data[key] = xml.getAttribute(value);
            if (!data[key])
                continue;
            else if (data[key].toLowerCase() === "true" || data[key].toLowerCase() === "false")
                data[key] = funcUtils.convertToBoolean(data[key]);
            else {
                data[key] = this.replaceDataDesignators(data[key], workbook, cell);
                if (!isNaN(Number(data[key])))
                    data[key] = Number(data[key]);
            }
        }
        data.result = true;
        for (const required of requiredList) {
            if (!data[required]) {
                data.result = false;
                workbook.log(Log.typeE, `${cell.worksheet.name}!${cell.row}: '${xml.rawTagName}' node, '${required}' attribute not specified`);
            }
        }
        return data;
    }
}
module.exports = dataDesignator;