    //         const copyValues = copyFromRange.values(workbook);
    //         const worksheet = workbook.getWorksheet(this.sheetName);
    //         const rowBottom = !isNaN(this.model.bottom) ? this.bottom : worksheet.rowCount;
    //         const columnRight = !isNaN(this.model.right) ? this.right : worksheet.columnCount;
    //         for (let row = this.top; row <= rowBottom; row++) {
