class FileCopyStatus {
    constructor(file) {
        this.file = file || "";
        this.directory = "";
        this.fromFile = "";
        this.toFile = "";
        this.message = "";
        this.severity = "";
        this.success = false;
        this.copied = false;
    }
    setFileCopyMessage(severity, message) {
        this.severity = severity;
        this.message = message;
    }
}
module.exports = FileCopyStatus;