const JSZip = require('jszip');
const {PassThrough} = require('readable-stream');
const StreamBuf = require('exceljs/lib/utils/stream-buf');
const {bufferToString} = require('exceljs/lib/utils/browser-buffer-decode');
const StylesXform = require('exceljs/lib/xlsx/xform/style/styles-xform');
const CoreXform = require('exceljs/lib/xlsx/xform/core/core-xform');
const SharedStringsXform = require('exceljs/lib/xlsx/xform/strings/shared-strings-xform');
const RelationshipsXform = require('exceljs/lib/xlsx/xform/core/relationships-xform');
const AppXform = require('exceljs/lib/xlsx/xform/core/app-xform');
const WorkbookXform = require('exceljs/lib/xlsx/xform/book/workbook-xform');
const WorksheetXform = require('exceljs/lib/xlsx/xform/sheet/worksheet-xform');
//const DrawingXform = require('exceljs/lib/xlsx/xform/drawing/drawing-xform');
const DrawingXform = require('./xform/drawing/drawing1-xform');
const TableXform = require('exceljs/lib/xlsx/xform/table/table-xform');
// *** ADD ***
// const process = require("process");
const utf8decoder = new TextDecoder();
const CtrlPropXform = require("./xform/control/ctrl-prop-xform");
const ChartSpaceXform = require("./xform/chart/chart-space-xform");
const ChartColorStyleXform = require("./xform/chart/chart-color-style-xform");
const ChartStyleXform = require("./xform/chart/chart-style-xform");
const ThemeXform = require("./xform/theme/theme-xform");
const ThemeOverrideXform = require("./xform/theme/theme-override-xform");
require("./xform/drawing/drawing-xform");
// const DocumentXform = require("./xform/document/document-xform");
// const NotesXform = require("./xform/document/notes-xform");
// *** ADD END ***

const XLSX = require('exceljs/lib/xlsx/xlsx');
Object.defineProperties(XLSX.prototype, {
    reconcile: {
        value: function(model, options) {
            const workbookXform = new WorkbookXform();
            const worksheetXform = new WorksheetXform(options);
            const drawingXform = new DrawingXform();
            const tableXform = new TableXform();

            workbookXform.reconcile(model);

            // reconcile drawings with their rels
            const drawingOptions = {
                media: model.media,
                mediaIndex: model.mediaIndex,
            };
            Object.keys(model.drawings).forEach(name => {
                const drawing = model.drawings[name];
                const drawingRel = model.drawingRels[name];
                if (drawingRel) {
                    drawingOptions.rels = drawingRel.reduce((o, rel) => {
                        o[rel.Id] = rel;
                        return o;
                    }, {});
                    (drawing.anchors || []).forEach(anchor => {
                        if (!anchor) {
                            console.log("anchor undefined");
                            return;
                        }
                        const hyperlinks = anchor.picture && anchor.picture.hyperlinks;
                        if (hyperlinks && drawingOptions.rels[hyperlinks.rId]) {
                            hyperlinks.hyperlink = drawingOptions.rels[hyperlinks.rId].Target;
                            delete hyperlinks.rId;
                        }
                        // *** ADD ***
                        if (anchor.graphicFrame && anchor.graphicFrame.graphic && anchor.graphicFrame.graphic.graphicData && anchor.graphicFrame.graphic.graphicData.chart) {
                            const rId = anchor.graphicFrame.graphic.graphicData.chart.attributes.rid;
                            const chartRel = drawingRel.filter(rel => rel.Id === rId)[0];
                            const match = chartRel.Target.match(/..\/charts\/(chart\d+)/);
                            if (match) {
                                anchor.graphicFrame.chart = {name: match[1], theme: undefined, colors: undefined, style: undefined};
                                Object.entries(model.chartRels).forEach(rel => {
                                    rel[1].forEach(r => {
                                        if (r.Id === rId) {
                                            let match = r.Target.match(/..\/theme\/(theme(Override)?\d+).xml/);
                                            if (match)
                                                anchor.graphicFrame.chart.theme = match[1];
                                            match = r.Target.match(/(colors\d)+.xml/);
                                            if (match)
                                                anchor.graphicFrame.chart.colors = match[1];
                                            match = r.Target.match(/(style\d+).xml/);
                                            if (match)
                                                anchor.graphicFrame.chart.style = match[1];
                                        }
                                    });
                                });
                            }
                        }
                        // *** ADD END ***
                    });
                    drawingXform.reconcile(drawing, drawingOptions);
                }
            });

            // reconcile tables with the default styles
            const tableOptions = {
                styles: model.styles,
            };
            Object.values(model.tables).forEach(table => {
                tableXform.reconcile(table, tableOptions);
            });

            const sheetOptions = {
                styles: model.styles,
                sharedStrings: model.sharedStrings,
                media: model.media,
                mediaIndex: model.mediaIndex,
                date1904: model.properties && model.properties.date1904,
                drawings: model.drawings,
                comments: model.comments,
                tables: model.tables,
                vmlDrawings: model.vmlDrawings,
                // *** ADD ***
                charts: model.charts,
                chartColors: model.chartColors,
                chartStyles: model.chartStyles,
                alternateContent: model.alternateContent,
                ctrlProps: model.ctrlProps,
                drawingRels: model.drawingRels,
                themes: model._themes,
                document: model.document,
                // *** ADD END ***
            };
            model.worksheets.forEach(worksheet => {
                worksheet.relationships = model.worksheetRels[worksheet.sheetNo];
                worksheetXform.reconcile(worksheet, sheetOptions);
            });

            // delete unnecessary parts
            delete model.worksheetHash;
            delete model.worksheetRels;
            delete model.globalRels;
            delete model.sharedStrings;
            delete model.workbookRels;
            delete model.sheetDefs;
            delete model.styles;
            delete model.mediaIndex;
            delete model.drawings;
            delete model.drawingRels;
            delete model.vmlDrawings;
            // *** ADD ***
            delete model.chartRels;
            delete model.ctrlProps;
            delete model.alternateContent;
            // *** ADD END ***
        }
    },
    _processThemeEntry: {
        value: async function(entry, model, name) {
            await new Promise((resolve, reject) => {
                // TODO: stream entry into buffer and store the xml in the model.themes[]
                const stream = new StreamBuf();
                entry.on('error', reject);
                stream.on('error', reject);
                stream.on('finish', () => {
                    model.themes[name] = stream.read().toString();
                    // *** ADD ***
                    this._process_ThemeEntry(model.themes[name], model, name);
                    resolve();
                    // *** ADD END ***
                    // model.themes[name] = stream.read().toString();
                    // // *** ADD ***
                    // // parse the xml into a js object
                    // parser.parseString(model.themes[name], {}, (err, data) => {
                    //     model._themes[name] = data;
                    //     resolve();
                    // });
                    // resolve();
                    // *** ADD END / MODIFY ***
                });
                entry.pipe(stream);
            });
        }
    },
    // *** ADD ***
    _process_ThemeEntry: {
        value: async function(data, model, name) {
            const xform = name.indexOf("Override") === -1 ? new ThemeXform() : new ThemeOverrideXform();
            model._themes[name] = await xform.parseStream(data);
        }
    },
    _processChartRelsEntry: {
        value: async function(entry, model, name) {
            const xform = new RelationshipsXform();
            model.chartRels[name] = await xform.parseStream(entry);
        }
    },
    _processChartEntry: {
        value: async function(entry, model, name) {
            // const time = Date.now();
            // console.log(Date.now() + " start:" + name);
            const xform = new ChartSpaceXform();
            model.charts[name] = await xform.parseStream(entry);
            // console.log(Date.now() + " end:" + name + ":" + (Date.now()-time));
        }
    },
    _processChartColorsEntry: {
        value: async function(entry, model, name) {
            const xform = new ChartColorStyleXform();
            model.chartColors[name] = await xform.parseStream(entry);
        }
    },
    _processChartStyleEntry: {
        value: async function(entry, model, name) {
            const xform = new ChartStyleXform();
            model.chartStyles[name] = await xform.parseStream(entry);
        }
    },
    _processCtrlPropEntry: {
        value: async function(entry, model, name) {
            const xform = new CtrlPropXform();
            model.ctrlProps["../ctrlProps/" + name + ".xml"] = await xform.parseStream(entry);
        }
    },
    _processDrawingEntry: {
        value: async function(entry, model, name) {
            const xform = new DrawingXform();
            const drawing = await xform.parseStream(entry);
            model.drawings[name] = drawing;
        }
    },
    // *** ADD END ***
    load: {
        value: async function (data, options) {
            let buffer;
            if (options && options.base64) {
                buffer = Buffer.from(data.toString(), 'base64');
            } else {
                buffer = data;
            }

            const model = {
                worksheets: [],
                worksheetHash: {},
                worksheetRels: [],
                themes: {},
                media: [],
                mediaIndex: {},
                drawings: {},
                drawingRels: {},
                comments: {},
                tables: {},
                vmlDrawings: {},
                // *** ADD ***
                _themes: {},
                chartRels: {},
                charts: {},
                chartColors: {},
                chartStyles: {},
                ctrlProps: {},
                document: {},
                // *** ADD END ***
            };

            const zip = await JSZip.loadAsync(buffer);
            for (const entry of Object.values(zip.files)) {
                /* eslint-disable no-await-in-loop */
                if (!entry.dir) {
                    let entryName = entry.name;
                    if (entryName[0] === '/') {
                        entryName = entryName.substring(1);
                    }
                    // console.log(Date.now() + ":" + entryName);
                    let stream;
                    if (entryName.match(/xl\/media\//) ||
                        // themes are not parsed as stream
                        entryName.match(/xl\/theme\/([a-zA-Z0-9]+)[.]xml/)) {
                        // if (process.browser)
                        //     continue;
                        stream = new PassThrough();
                        stream.write(await entry.async('uint8array'));
                    } else {
                        // use object mode to avoid buffer-string convention
                        stream = new PassThrough({
                            writableObjectMode: true,
                            readableObjectMode: true,
                        });
                        let content;
                        // https://www.npmjs.com/package/process
                        if (process.browser) {
                            // running in browser, use TextDecoder if possible
                            content = bufferToString(await entry.async('uint8array'));
                        } else {
                            // running in node.js
                            content = await entry.async('string');
                        }
                        const chunkSize = 16 * 1024;
                        for (let i = 0; i < content.length; i += chunkSize) {
                            stream.write(content.substring(i, i + chunkSize));
                        }
                    }
                    stream.end();
                    // console.log(entryName);
                    switch (entryName) {
                        case '_rels/.rels':
                            model.globalRels = await this.parseRels(stream);
                            break;

                        case 'xl/workbook.xml': {
                            const workbook = await this.parseWorkbook(stream);
                            model.sheets = workbook.sheets;
                            model.definedNames = workbook.definedNames;
                            model.views = workbook.views;
                            model.properties = workbook.properties;
                            model.calcProperties = workbook.calcProperties;
                            break;
                        }

                        case 'xl/_rels/workbook.xml.rels':
                            model.workbookRels = await this.parseRels(stream);
                            break;

                        case 'xl/sharedStrings.xml':
                            model.sharedStrings = new SharedStringsXform();
                            await model.sharedStrings.parseStream(stream);
                            break;

                        case 'xl/styles.xml':
                        // case 'word/styles.xml': // *** ADD ***
                            model.styles = new StylesXform();
                            await model.styles.parseStream(stream);
                            break;

                        case 'docProps/app.xml': {
                            const appXform = new AppXform();
                            const appProperties = await appXform.parseStream(stream);
                            model.company = appProperties.company;
                            model.manager = appProperties.manager;
                            break;
                        }

                        case 'docProps/core.xml': {
                            const coreXform = new CoreXform();
                            const coreProperties = await coreXform.parseStream(stream);
                            Object.assign(model, coreProperties);
                            break;
                        }

                        // *** ADD ***
                        // case 'word/_rels/document.xml.rels':
                        //     model.documentRels = await this.parseRels(stream);
                        //     break;
                        //
                        // case 'word/document.xml': {
                        //     const documentXform = new DocumentXform();
                        //     const document = await documentXform.parseStream(stream);
                        //     model.document.body = document.body;
                        //     break;
                        // }
                        // case 'word/endnotes.xml': {
                        //     const endNotesXform = new NotesXform({tag: "w:endnotes"});
                        //     model.document.endNotes = await endNotesXform.parseStream(stream);
                        //     break;
                        // }
                        // case 'word/footnotes.xml': {
                        //     const footNotesXform = new NotesXform({tag: "w:footnotes"});
                        //     model.document.footNotes = await footNotesXform.parseStream(stream);
                        //     break;
                        // }
                        // *** ADD END ***

                        default: {
                            let match = entryName.match(/xl\/worksheets\/sheet(\d+)[.]xml/);
                            if (match) {
                                await this._processWorksheetEntry(stream, model, match[1], options, entryName);
                                break;
                            }
                            match = entryName.match(/xl\/worksheets\/_rels\/sheet(\d+)[.]xml.rels/);
                            if (match) {
                                await this._processWorksheetRelsEntry(stream, model, match[1]);
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/theme\/([a-zA-Z0-9]+)[.]xml/); // *** MODIFY
                            if (match) {
                                await this._processThemeEntry(stream, model, match[2]); // MODIFY
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/media\/([a-zA-Z0-9]+[.][a-zA-Z0-9]{3,4})$/);    // *** MODIFY
                            if (match) {
                                await this._processMediaEntry(stream, model, match[2]); // *** MODIFY
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/drawings\/([a-zA-Z0-9]+)[.]xml/);  // *** MODIFY
                            if (match) {
                                await this._processDrawingEntry(stream, model, match[2]);   // *** MODIFY
                                break;
                            }
                            match = entryName.match(/xl\/(comments\d+)[.]xml/);
                            if (match) {
                                await this._processCommentEntry(stream, model, match[1]);
                                break;
                            }
                            match = entryName.match(/xl\/tables\/(table\d+)[.]xml/);
                            if (match) {
                                await this._processTableEntry(stream, model, match[1]);
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/drawings\/_rels\/([a-zA-Z0-9]+)[.]xml[.]rels/);   // *** MODIFY
                            if (match) {
                                await this._processDrawingRelsEntry(stream, model, match[2]);   // *** MODIFY
                                break;
                            }
                            match = entryName.match(/xl\/drawings\/(vmlDrawing\d+)[.]vml/);
                            if (match) {
                                await this._processVmlDrawingEntry(stream, model, match[1]);
                                break;
                            }
                            // *** ADD ***
                            match = entryName.match(/([xl]|[word])\/charts\/_rels\/(chart\d+)[.]xml.rels/);   // *** MODIFY
                            if (match) {
                                await this._processChartRelsEntry(stream, model, match[2]); // *** MODIFY
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/charts\/(chart\d+)[.]xml/);    // *** MODIFY
                            if (match) {
                                await this._processChartEntry(stream, model, match[2]); // *** MODIFY
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/charts\/(colors\d+)[.]xml/);
                            if (match) {
                                await this._processChartColorsEntry(stream, model, match[2]);
                                break;
                            }
                            match = entryName.match(/([xl]|[word])\/charts\/(style\d+)[.]xml/);
                            if (match) {
                                await this._processChartStyleEntry(stream, model, match[2]);
                                break;
                            }
                            match = entryName.match(/xl\/ctrlProps\/(ctrlProp\d+)[.]xml/);
                            if (match) {
                                await this._processCtrlPropEntry(stream, model, match[1]);
                                break;
                            }
                            // *** ADD END ***
                        }
                    }
                }
            }

            this.reconcile(model, options);

            // apply model
            this.workbook.model = model;
            return this.workbook;
        }
    }
});
