const CompositeExtXform = require('../composite-ext-xform');
const FloatXform = require('exceljs/lib/xlsx/xform/simple/float-xform');
const funcUtils = require("../../../../utils/func-utils");

class NumericPointXform extends CompositeExtXform {
    constructor() {
        super();

        this.tag = "c:pt";
        this.map = {
            "c:v": new FloatXform({tag: "c:v"}),
        };
    }
}
module.exports = NumericPointXform;
