const message = require("./message");
class TryCatch {
 // constructor($msg) {
 //   this.$alert = $msg || $(".message-alert");
 // }

  async tryCatch(callback, arg) {
    try {
        await callback(arg);
        return true;
    } catch (error) {
        console.error(error);
        message.alert(`${error.name}: ${error.message}`, "danger");
        return false;
    }
  }

  try(callback, arg) {
    try {
        callback(arg);
        return true;
    } catch (error) {
        console.error(error);
        message.alert(`${error.name}: ${error.message}`, "danger");
        return false;
    }
  }
}

module.exports = TryCatch;

