const Constants = require('../../../webconvert/lib/src/constants');
const funcUtils = require('../../../webconvert/lib/utils/func-utils');
const Log = require('../../../webconvert/lib/utils/log');

const messageAlertClass = '.message-alert';

const message = {
  alert(message = '', type = 'danger', querySelector = messageAlertClass, options = {}) {
    if (querySelector === undefined)
      querySelector = messageAlertClass;
    const alert = document.querySelectorAll(querySelector);
    if (!alert) {
      console.log(`'${querySelector}' querySelectorAll not found`);
      return;
    }
    for (const a of alert)
      a.innerHTML = '';
    if (!message || !message.length)
      return;
    if (Array.isArray(message)) {
      const div = document.createElement('div');
      const id = `${querySelector.replace(/[.]/g, '')}-select${Math.floor(Math.random() * (1000 - 0 + 1) + 0)}`;
      const label = document.createElement('label');
      label.setAttribute('for', id);
      label.innerHTML = 'Log';
      div.append(label);
      div.classList.add('pt-4');
      div.classList.add('ps-0');
      const select = document.createElement('select');
      select.setAttribute('id', id);
      div.append(select);
      for (const m of message) {
        const option = document.createElement('option');
        option.innerHTML = m;
        select.appendChild(option);
      }
      select.setAttribute('size', message.length);
      message = div.outerHTML;
    }
    for (let i = 0; i < alert.length; i++) {
      const alertDivClass = alert[i].getAttribute('data-alert-div-class') || '';
      const alertButtonClass = alert[i].getAttribute('data-alert-button-class') || '';
      const wrapper = document.createElement('div')
      wrapper.innerHTML = [
        `<div class='alert alert-${type} alert-dismissible small ${alertDivClass}' role='alert'>`,
        `   <div>${message}</div>`,
        `   <button type='button' class='btn-close ${alertButtonClass}' data-bs-dismiss='alert' aria-label='Close'></button>`,
        `</div>`
      ].join('')
      alert[i].innerHTML = '';
      alert[i].appendChild(wrapper);
    }
    if ((options.scrollAlert === undefined && querySelector === messageAlertClass && message.length) || options.scrollAlert)
      this.scrollAlert(querySelector);
  },

  alertDanger(message = '', querySelector = messageAlertClass, options = {}) {
    this.alert(message, 'danger', querySelector, options);
  },

  alertInfo(message = '', querySelector = messageAlertClass, options = {}) {
    this.alert(message, 'info', querySelector, options);
  },

  alertSuccess(message = '', querySelector = messageAlertClass, options = {}) {
    this.alert(message, 'success', querySelector, options);
  },

  alertWarning(message = '', querySelector = messageAlertClass, options = {}) {
    this.alert(message, 'warning', querySelector, options);
  },

  alertClear(querySelector = messageAlertClass) {
    this.alert('', '', querySelector);
  },

  alertConcat(message, type='danger', querySelector = messageAlertClass, atEnd = false) {
    if (querySelector === undefined)
      querySelector = messageAlertClass;
    const alert = document.querySelector(querySelector);
    if (!alert) {
      console.log(`'${querySelector}' querySelector not found`);
      return;
    }
    const alertText = $(alert).find('div div div');
    let messages = alertText ? alertText[0].innerHTML : '';
    // messages = messages.length ? `<br>${messages}` : messages;
    // type = type === 'success' && messages.length ? 'warning' : type;
    if (!atEnd)
      this.alert(`${message}<br>${messages}`, type, querySelector);
    else
      this.alert(`${messages}<br>${message}`, type, querySelector);
  },

  scrollAlert(querySelector = querySelector = '.message-alert', index = -1) {
    const alerts = document.querySelectorAll(querySelector);
    const alert = alerts[index >= 0 ? index : alerts.length - 1];
    alert.scrollIntoView({behavior: 'smooth'} );
    // $('html, body').scrollTop( $(document).height() - $(window).height() );
  },

  check(workbook, options, querySelector = messageAlertClass) {
    const errorMsg = [];

    if (options.checkIsPC && options.Office && options.Office.context.platform !== Office.PlatformType.PC)
      errorMsg.push('Action only valid on Excel desktop (PC/Windows)');
    if (options.checkIsModel && !workbook.isValidModel)
      errorMsg.push('Workbook is not a valid TEI Model');
    if (options.checkHasSheetTags) {
      for (const sheetTag of Constants.sheetTags.rbc) {
        const worksheet = workbook.getWorksheetByTagName(sheetTag);
        if (!worksheet)
          errorMsg.push(Log.messageDescription(Log.MessageNum.TemplateSheetNotFound, {worksheet: this, sheetTag: sheetTag}));
        // errorMsg.push(`'${sheetTag}': No sheet found with tag`);
      }
    }

    if (options.checkSheetXml && !workbook.activeSheet.hasMetadata)
      errorMsg.push(`${workbook.activeSheet.name}: Sheet has no XML`);

    if (options.checkUsableSheet && workbook.activeSheet.xml && !funcUtils.convertToBoolean(workbook.activeSheet.xml.getAttribute(Constants.worksheet.attributes.usableSheet)))
      errorMsg.push(`${workbook.activeSheet.name}: Sheet attribute '${Constants.worksheet.attributes.usableSheet}' is marked false`);

    if (options.checkRowXml && options.rowNumber && !workbook.activeSheet.getRow(options.row).xml)
      errorMsg.push(`${workbook.activeSheet.name}: Row ${options.rowNumber}: Row has no XML`);

    if (options.checkEquationSection && options.rowNumber) {
      const row = workbook.activeSheet.getRow(options.rowNumber);
      if (!row.section || !workbook.activeSheet.equationSections.includes(row.section))
        errorMsg.push(`${workbook.activeSheet.name}: Row ${options.rowNumber}: Selected row must be part of an equation section`);
    }

    if (options.checkRowVariable && options.rowNumber && workbook.activeSheet.getRow(options.rowNumber).xml && workbook.activeSheet.getRow(options.rowNumber).xml.getAttribute(Constants.row.attributes.tag) !== Constants.row.tags.variableDesignator)
      errorMsg.push(`${workbook.activeSheet.name}: Row ${options.row}: Row must be a 'variable row': must have tag of '${Constants.row.tags.variableDesignator}'`);

    if (options.checkVariablesDelete && options.activeRange) {
      const rows = options.activeRange ? options.activeRange.getRows(workbook) : [];
      const variableRows = rows.filter(r => r.xml && r.xml.getAttribute(Constants.row.attributes.tag) === Constants.row.tags.variableDesignator);
      if (variableRows.length !== rows.length)
        errorMsg.push(`${workbook.activeSheet.name}: All rows in selected range must be a 'variable row': must have tag of '${Constants.row.tags.variableDesignator}'`);
      else if (rows.filter(r => r.xml && r.section && r.section === rows[0].section).length !== rows.length)
        errorMsg.push(`Selected range can only include rows from one section`);
      else if (rows.length === rows[0].section.getRowsByTag(Constants.row.tags.variableDesignator).length)
        errorMsg.push(`Can't delete all variable rows`);
    }
    if (options.checkCostBenefit) {
      if (!workbook.activeSheet.xml)
        errorMsg.push(`${workbook.activeSheet.name}: Sheet has no XML`);
      else {
        const tag = workbook.activeSheet.xml.getAttribute(Constants.worksheet.attributes.tag);
        if (tag !== Constants.worksheet.values.benefits && tag !== Constants.worksheet.values.costs) {
          const sheetB = workbook.getWorksheetByTagName(Constants.worksheet.values.benefits);
          const sheetC = workbook.getWorksheetByTagName(Constants.worksheet.values.costs);
          if ((!sheetB && !sheetC) || ((!sheetB || sheetB.name !== workbook.activeSheet.name) && (!sheetC || sheetC.name !== workbook.activeSheet.name)))
            errorMsg.push(`${workbook.activeSheet.name}: Sheet data must have attribute '${Constants.worksheet.attributes.tag}' of '${Constants.worksheet.values.benefits}' or '${Constants.worksheet.values.costs}'`);
        }
      }
    }

    if (options.checkSheetProtection && workbook.activeSheet.sheetProtection)
      errorMsg.push(`${workbook.activeSheet.name}: Sheet is protected`);

    if (options.checkTemplateSheet && !workbook.activeSheet.getSheetTemplate())
      errorMsg.push(`${workbook.activeSheet.name}: Template sheet not found`);

    this.alert(errorMsg.length ? errorMsg.join('<br>') : '', 'danger', querySelector);
    return !errorMsg.length;
  },

  checkActiveRange(activeRange, options = {checkActiveCell: false, checkEntireRow: false, checkEntireColumn: false, checkOneRow: false}, querySelector) {
    const errorMsg = activeRange.check(options);
    this.alert(errorMsg.length ? errorMsg.join('<br>') : '', 'danger', querySelector);
    return !errorMsg.length;
  },

}
module.exports = message;