const fs = require("fs");
const path = require("path");
const fileCopyStatus = require("./file-copy-status.js");
const Log = require("./log");
const fileUtils = {
    fileExists: function(fn) {
        try {
            const stats = fs.statSync(fn);
            return stats.isFile();
        } catch(e) {
            return false;
        }
    },
    directoryExists: function(dir) {
        try {
            const stats = fs.statSync(dir);
            return stats.isDirectory();
        } catch(e) {
            return false;
        }
    },
    findFileInFolder: function(dir, file) {
        if (!this.directoryExists(dir))
            return "";
        let foundFile = "";
        let filesDirs = fs.readdirSync(dir);
        filesDirs.every(fileDir => {
            let fullFile = path.join(dir, fileDir);
            let stat = fs.statSync(fullFile);
            if (stat.isFile()) {
                if (fileDir === file) {
                    foundFile = fullFile;
                    return false;
                }
            } else if (stat.isDirectory()) {
               foundFile = this.findFileInFolder(fullFile, file);
               if (foundFile.length)
                   return false;
            }
            return true;
        });
        return foundFile;
    },
    copyFileToFolder: function(fnw, dirs, dstDir) {
        const fcs = new fileCopyStatus(fnw);
        if (!fnw.length) {
            fcs.setFileCopyMessage(Log.typeE, "File name not specified");
            return fcs;
        }
        // let sfn = fnw.replace(/\//g, "\\");
        let sfn = fnw;
        if (!this.fileExists(sfn)) {
            dirs.every(dir => {
               // let fn = dir + (sfn.substring(0,1) === "\\" ? "" : "\\") + sfn;
               let fn = path.join(dir, sfn);
               // fn = fn.replace(/\\\\/g, "\\");
               if (this.fileExists(fn)) {
                   sfn = fn;
                   fcs.directory = dir;
                   return false;
               }
               return true;
            });
            if (!this.fileExists(sfn)) {
                fcs.setFileCopyMessage(Log.typeE, `File not found: ${sfn}`);
                return fcs;
            }
        }
        let dfn = sfn;
        dirs.forEach( dir => {
            dfn = dfn.replace(dir + path.sep, "");
        });

        let ds = dfn.split(path.sep);
        let ddir = dfn.substring(0, dfn.length - ds[ds.length-1].length-1);
        let d = path.join(dstDir, ddir);
        try {
            fs.mkdirSync(d, {recursive: true});
        } catch(error) {
            fcs.setFileCopyMessage(Log.typeE, "Error creating directories: " + d);
            return fcs;
        }

        let fn = dfn;
        let cfn = "";
        dfn = path.join(d, ds[ds.length-1]);
        // dfn = dfn.replace("\\\\", "\\");
        fcs.fromFile = sfn;
        fcs.toFile = dfn;
        if (this.fileExists(dfn)) {
            let statsSrc = fs.statSync(sfn);
            let statsDst = fs.statSync(dfn);
            if (statsSrc.mtimeMs !== statsDst.mtimeMs)
                cfn = sfn;
        } else
            cfn = sfn;

        fcs.success = true;
        if (cfn.length) {
            fs.copyFileSync(cfn, dfn);
            fcs.setFileCopyMessage(Log.typeI, "File copy: " + fn, );
            fcs.copied = true;
        } else
            fcs.setFileCopyMessage(Log.typeI, "File exists and identical: " + fn);
        return fcs;
    },
    copyFilesToFolder: function(fn, srcDirs, dstDir) {
        const fcs = new fileCopyStatus(fn);
        let fileCopyStatusArray = [];
        if (fn.length === 0) {
            fcs.setFileCopyMessage(Log.typeE, "No files specified");
            return [fcs];
        }
        srcDirs = srcDirs ? srcDirs : [];
        fn = fn.replace(/\//g, "\\");
        let x = fn.lastIndexOf("\\");
        let d = x >= 0 ? fn.substring(0, x) : "";
        let srcDir = "";
        let srcDirFull = "";
        let files = [];
        if (fn.slice(-1) === "*") {
            if (this.directoryExists(d, true))
                srcDir = d;
            srcDirs.every( dir => {
                let dirFull = path.join(dir, d);
                if (this.directoryExists(dirFull, true)) {
                    srcDir = d;
                    srcDirFull = dirFull;
                    return false;
                } else
                    return true;
            });
            if (srcDir.length === 0) {
                fcs.setFileCopyMessage(Log.typeE, "Invalid wildcard directory specified: " + fn);
                return [fcs];
            }
            files = fs.readdirSync(srcDirFull);
            if (files.length === 0) {
                fcs.setFileCopyMessage(Log.typeI, "No files to copy: " + fn);
                return [fcs];
            }
            files.forEach((file, index) => {
                files[index] = path.join(srcDir, files[index]);
            });
            // return [];
        } else
            files.push(fn);

        files.forEach(file => {
            fileCopyStatusArray.push(this.copyFileToFolder(file, srcDirs, dstDir));
        });
        return fileCopyStatusArray;
    },
    deleteFilesNotInList: function(directory, file, list, filesDeleted) {
        let dirEntries = fs.readdirSync(file);
        for (const dirEntry of dirEntries) {
            const fullName = path.join(file, dirEntry);
            const stats = fs.statSync(fullName);
            if (stats.isDirectory())
                this.deleteFilesNotInList(directory, fullName, list, filesDeleted);
            else {
                const fn = path.relative(directory, fullName)
                if (!list.includes(fn)) {
                    filesDeleted.push(fn);
                    fs.unlinkSync(fullName);
                }
            }
        }
    },
    deleteDirectoriesEmpty: function(directory, file, list, directoriesDeleted, excludeDirectoryList) {
        let dirEntries = fs.readdirSync(file);
        for (const dirEntry of dirEntries) {
            const fullName = path.join(file, dirEntry);
            const stats = fs.statSync(fullName);
            if (stats.isDirectory())
                this.deleteDirectoriesEmpty(directory, fullName, list, directoriesDeleted, excludeDirectoryList);
        }
        // reload in case directories deleted
        dirEntries = fs.readdirSync(file);
        if (!dirEntries.length) {
            let exclude = false;
            for (const excludeDirectory of excludeDirectoryList) {
                if (file.substring(0, excludeDirectory.length) === excludeDirectory) {
                    exclude = true;
                    break;
                }
            }
            if (!exclude) {
                fs.rmdirSync(file);
                const fn = path.relative(directory, file);
                directoriesDeleted.push(fn);
            }
        }
    },
}
module.exports = fileUtils;