    loadXml : {
        value:
            async function() {
                if (!this.isAddIn) {
                    this.isValidModel = true;
                    parser = new (require('../../../../../web/js/forrester/parser')).Parser;
                    this.workbookData.periods = this.getDefinedNameValue(Constants.workbook.definedNames.numberPeriods);
                    this.workbookData.maxPeriods = this.getDefinedNameValue(Constants.workbook.definedNames.maxPeriods);
                    if (this.workbookData.maxPeriods > this.workbookData.periods) {
                        const range = new Range(this.definedNames.getRanges(Constants.workbook.definedNames.maxPeriods).ranges[0]);
                        const cell = this.getWorksheet(range.sheetName).getCell(range.top, range.left);
                        cell.value = this.workbookData.periods;
                    }
                }

                this.version.xmlUpVersion = this.isValidModel && upVersion.isFixApplied(this, upVersion.fixAttributes.xmlUpVersion);
                this.version.htmlUpVersion = this.isValidModel && upVersion.isFixApplied(this, upVersion.fixAttributes.htmlUpVersion);

                this.eachSheet(worksheet => {
                    if (this.isAddIn && worksheet.xlc && worksheet.refresh !== undefined && !worksheet.refresh)
                        return;
                    worksheet.loadXml();
                    if (worksheet.delete)
                        this.removeWorksheet(worksheet.id);
                });

                if (this.xmlModelWebApp && !this.isAddIn) {
                    // this.xmlModel.setAttribute("markedFinal", "True");
                    const date = new Date();
                    this.xmlModelWebApp.setAttribute("dateTime", date.toLocaleString());
                    this.xmlModelWebApp.setAttribute("version",
                        [`${date.getFullYear().toString()}${(date.getMonth() + 1).toString().padStart(2, "0")}${date.getDate().toString().padStart(2, "0")}`,
                            `${date.getHours().toString().padStart(2, "0")}${date.getMinutes().toString().padStart(2, "0")}${date.getSeconds().toString().padStart(2, "0")}`].join(''));
                    this.xmlModelWebApp.setAttribute("workbookName", this.workbookData.name);
                    this.xmlModelWebApp.setAttribute("workbookNameShort", this.workbookData.name.replace(".xlsx", ""));
                    this.xmlModelWebApp.setAttribute("projectDir", `${this.webInfo.project.folder.web}\\`);
                    // initials
                    const names = this.lastModifiedBy.split(" ");
                    let initials = "";
                    names.forEach(name => initials += name.substring(0, 1));
                    this.xmlModelWebApp.setAttribute("initials", initials);
                    this.xmlModelWebApp.setAttribute("modelVersion", this.xmlModel.getAttribute("version"));
                    // cell.value = data.toString();   // put back the changes
                    // cell.value = cell.value.replace(/\\\\/g, "\\");

                    this.webInfo.project.file.html = this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.htmlFile) || this.webInfo.project.file.html;
                    const zipFilePrefix = this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.zipFilePrefix) || "";
                    this.webInfo.project.file.zip = path.join(this.webInfo.project.folder.webApp.main, `${zipFilePrefix}${path.basename(this.workbookData.name, path.extname(this.workbookData.name))}${extension}.zip`);
                }
                if (this.xmlModelWebApp) {
                    this.config = {
                        namespaceVars: "lgtvars",
                        namespaceBootstrap: "bsvars",
                        namespace: "webTool",
                        bootstrapVersion: this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.bootstrap) || "5",
                        saveOnUpdate: funcUtils.convertToBoolean(this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.saveOnUpdate)) || false,
                        // minimize: funcUtils.convertToBoolean(this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.minimize)) || false,
                        mainstay: funcUtils.convertToBoolean(this.xmlModelWebApp.getAttribute(Constants.workbook.attributes.mainstay)) || false,
                    }
                }
                if (this.xmlModel) {
                    this.version.date = this.xmlModel.getAttribute(Constants.workbook.attributes.version);
                    this.version.isVersionViews = this.version.date >= "20221222";
                }

                if (!this.isAddIn) {
                    this.doTranslations();

                    this.eachSheet(worksheet => {
                        worksheet.eachRow(row => {
                            row.eachCell({includeEmpty: false}, cell => {
                                // must run formula (and shared formula properties) through parser to adjust formulas due to splicing columns
                                if (cell.type === Enums.ValueType.Formula) {
                                    // note that although updating the formula, the associated value is not necessarily adjusted
                                    // this assumes the function recalculates it in the web app
                                    if (cell.formulaType === Enums.FormulaType.Master) {
                                        // one thing this does is remove "_xlfn." as prefix to functions
                                        cell.model.formula = this.parseFormula(cell.formula, cell, null, true);
                                        if (cell.model.ref)
                                            cell.model.ref = this.parseFormula(cell.model.ref, cell, null, true);
                                    } else if (cell.formulaType === Enums.FormulaType.Shared)
                                        cell.model.sharedFormula = this.parseFormula(cell.model.sharedFormula, cell, null, true);
                                    // adjust the value if the formula refers to a deleted sheet
                                    for (const np of cell.namePrecedents) {
                                        if (!this.getDefinedNameCells(np).length)
                                            cell.model.result = Enums.ErrorValue.Ref;
                                    }
                                    for (const dp of cell.directPrecedents) {
                                        const split1 = dp.split(':');
                                        const split = split1[0].split('!');
                                        if (split.length > 1 && !this.getWorksheet(split[0].replace(/'/g, '')))
                                            cell.model.result = Enums.ErrorValue.Ref;
                                    }
                                }
                            });
                        });
                    });
                }
                if (this.isValidModel === undefined)
                    this.isValidModel = this.xmlModel !== undefined;
                return this.xmlModelWebApp;
            }
    },

