async function e2wDisplay() {
    return await Word.run(async (context) => {
        $html.e2wApplyButton.prop('disabled', true);
        await e2wGetTemplateVersion(context, 'Document number');
        $html.e2wTemplateVersion.html(data.templateVersion);
        if (!data.templateVersion.length) {
            message.alertDanger('Not a valid template file');
            return false;
        }
        $html.e2wApplyButton.prop('disabled', false);
        return true;
    });
}

async function e2wGetBookmarks(context) {
    const bodyRange = context.document.body.getRange(Word.RangeLocation.whole);
    const bookmarks = bodyRange.getBookmarks(true);
    await context.sync();
    data.bookmarks = [];
    for (const bookmark of bookmarks.value) {
        const range = context.document.getBookmarkRange(bookmark);
        data.bookmarks.push({name: bookmark, range: range.load('*, table/items')});
    }
    await context.sync();
}

async function e2wGetHeaders(context) {
    const wordDoc = context.document;
    const paragraphs = wordDoc.body.paragraphs.load('items');
    data.headers = [];
    await context.sync();
    for (let paragraph of paragraphs.items) {
        // we can use outlineLevel 1, 2, 3
        if (paragraph.styleBuiltIn === 'Heading1' || paragraph.styleBuiltIn === 'Heading2' || paragraph.styleBuiltIn === 'Heading3' ) {
            if (paragraph.text.trim().match(/^\w/))
                data.headers.push(paragraph);
        }
    }
    console.log('getHeaders')
}

async function e2wExcelFileSelected() {
    excelFile.files = $html.e2wExcelFile[0].files;
    if (await e2wDisplay()) {
        $html.e2wApplyButton.prop('disabled', true);
        message.alertInfo(`Loading: ${excelFile.files[0].name}`, '.message-alert-excel-file');
        // https://github.com/OfficeDev/office-js-snippets/blob/main/samples/excel/50-workbook/create-workbook.yaml
        excelFile.reader = new FileReader();
        excelFile.reader.onload = () => TryCatch.tryCatch(e2wExcelFileRead);
        // Read in the file as a data URL, and we can parse the Base64-encoded string.
        excelFile.reader.readAsArrayBuffer(excelFile.files[0]);
    }
}

async function e2wExcelFileRead(){
    // Now read in the file
    // // https://github.com/exceljs/exceljs/issues/832
    const buffer = excelFile.reader.result;
    workbook = new ExcelJS.Workbook();
    workbook.init(excelFile.files[0], true);
    await workbook.xlsx.load(buffer);
    console.log(`Read workbook files: ${excelFile.files[0].name}`);
    await workbook.loadXml(true);
    // check if any equations exists...
    if (!message.check(workbook, {checkIsModel: true}))
        return false;
    excelFile.fileRead = true;
    message.alertInfo(`Loaded: ${excelFile.files[0].name}`, '.message-alert-excel-file');

    e2wCategoriesDisplay();
    await e2wVariablesDisplay();
    e2wInputsIncludedDisplay();

    $html.e2wExcelFile.prop('disabled', false);
    $html.e2wApplyButton.prop('disabled', false);
}

function e2wCategoriesDisplay() {
    $html.e2wCategories.html('');
    const options = [];
    for (const sheetTag of Constants.sheetTags.bc) {
        const worksheet = workbook.getWorksheetByTagName(sheetTag);
        if (worksheet) {
            const sections = worksheet.equationSections.filter(s => funcUtils.convertToBoolean(s.start.xmlTableData.getAttribute(Constants.row.attributes.tableData.includeInTotals)));
            options.push(`${sections.length} ${sheetTag} included in total:`);
            for (const section of sections) {
                const variables = section.getRowsByTag(Constants.row.tags.variableDesignator);
                const titleRow = section.getRowByTag('Title');
                let title = '';
                if (titleRow) {
                    const cd = worksheet.getRow(titleRow.number).getColDataByName('title');
                    if (cd)
                        title = cd.cell.valueOrResult;
                }
                options.push(`&nbsp;&nbsp;${section.start.xmlTableData.getAttribute(Constants.row.attributes.reference)}: ${title} (${variables.length} variables)`);
            }
        }
    }
    for (const option of options) {
        const $option = $($html.option.clone());
        $option.html(option);
        $option.prop('disabled', true);
        $html.e2wCategories.append($option);
    }
    $html.e2wCategories.prop('size', options.length);
}

async function e2wGetTemplateVersion(context, prop) {
    data.templateVersion = '';
    const properties = context.document.properties.customProperties;
    properties.load("*");
    await context.sync();
    for (const property of properties.items) {
        if (property.key === prop) {
            for (const [key, value] of Object.entries(version)) {
                if (value === property.value) {
                    data.templateVersion = property.value;
                    break;
                }
            }
        }
    }
}

async function e2wVariablesAuthorDisplay() {
    return await Word.run(async (context) => {
        const properties = context.document.properties;
        properties.load('*');
        await context.sync();
        $html.e2wVariablesAuthor.val(properties.author);
    });
}


