const DrawingXform = require("exceljs/lib/xlsx/xform/drawing/drawing-xform");
require("./two-cell-anchor-xform");
const AlternateContentDrawingXform = require("./alternate-content-drawing-xform");

Object.defineProperties(DrawingXform.prototype, {

    // *** ADD ***
    parseOpen: {
        value: function (node) {
            if (this.parser) {
                this.parser.parseOpen(node);
                return true;
            }
            switch (node.name) {
                case this.tag:
                    // // *** ADD ***
                    if (!this.map['mc:AlternateContent'])
                        this.map['mc:AlternateContent'] = new AlternateContentDrawingXform();
                    // // *** ADDEND ***
                    this.reset();
                    this.model = {
                        anchors: [],
                    };
                    break;
                default:
                    this.parser = this.map[node.name];
                    if (this.parser) {
                        this.parser.parseOpen(node);
                    }
                    break;
            }
            return true;
        }
        // *** ADD END ***
    },
    parseClose: {
        variable:
            function (name) {
                if (this.parser) {
                    if (!this.parser.parseClose(name)) {
                        this.model.anchors.push(this.parser.model);
                        this.parser = undefined;
                    }
                    return true;
                }
                switch (name) {
                    case this.tag:
                        return false;
                    default:
                        // could be some unrecognised tags
                        return true;
                }
            }
    },
    reconcile: {
        variable:
            function (model, options) {
                model.anchors.forEach(anchor => {
                    if (anchor.br) {
                        this.map['xdr:twoCellAnchor'].reconcile(anchor, options);
                    } else {
                        this.map['xdr:oneCellAnchor'].reconcile(anchor, options);
                    }
                });
            }
    },

});