class Log {
    constructor(severityType, text, timers) {
        this.message = {
            severityType: severityType,
            text: text,
            timerFromStart: timers ? timers.start.getValue() : null,
            timerFromLast: timers ? timers.last.getValue() : null
        };
        if (timers) {
            timers.last.reset();
            timers.last.start();
        }
        console.log(this.text);
    }
    static severities = [
        {name: 'general', type: 0, text: 'G'},
        {name: 'information', type: 1, text: 'I'},
        {name: 'warning', type: 2, text: 'W'},
        {name: 'error', type: 3, text: 'E'},
    ];
    static get typeG() {
        return Log.severities.find(severity => severity.name === 'general').type;
    }
    static get typeI() {
        return Log.severities.find(severity => severity.name === 'information').type;
    }
    static get typeW() {
        return Log.severities.find(severity => severity.name === 'warning').type;
    }
    static get typeE() {
        return Log.severities.find(severity => severity.name === 'error').type;
    }
    get text() {
        let msg = `${(this.message.severityType !== null ? (Log.severities.find(severity => severity.type === this.message.severityType).text + ': ') : '')}${this.message.text}`;
        if (this.message.timerFromStart)
            msg += ` [${this.message.timerFromStart} from start, ${this.message.timerFromLast} from last]`;
        return msg;
    }
    static MessageNum = {
        TemplateSheetNotFound: 0,
        TemplateTemplateNotFound: 1,
    };
    static MessageDescription = [
        {number: this.MessageNum.TemplateSheetNotFound, description: `{row.address}{worksheet.name}, '{sheetTag}': Template sheet not found.`},
        {number: this.MessageNum.TemplateSheetNotFound, description: `{worksheet.name}, '{tableTag}': Table template not found in {templateSheet.name}`},
        ];
    static messageDescription(messageNum, options) {
        const msgDesc = this.MessageDescription.filter(md => md.number === messageNum)[0];
        if (!msgDesc)
            return '';
        let description = msgDesc.description;
        const regExp = /{([^}.]+)(.)?([^}]+)?}/g;
        const matches = description.matchAll(regExp);
        for (const match of matches) {
            let strReplace = '';
            const objectOrVariable = match[1];
            if (options[objectOrVariable]) {
                const ov = options[objectOrVariable];
                if (match.length < 4)
                    strReplace = ov;
                else if (match[3] && ov[match[3]])
                    strReplace = ov[match[3]];
            }
            description = description.replace(match[0], strReplace);
        }
        return description;
    }
}
module.exports = Log;